import { Segment } from 'base-segment';
// #----------------------------------------------------------------------------# //
// #---- Segment (/employee-record/credentials/segment-credentials-content) ----# //

class CredentialsContent extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $stateParams, ssFlagSvc) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentEmployeeRecordCredentialsCredentialsContentLogic'); // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
			employeeId: $stateParams.employeeId,
			// feature flag for the new credentials
			useNewCredentials: ssFlagSvc.checkState('fc-new-credentials-enabled'),
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = {}; // jshint ignore:line
	}
}

// #-- END Segment (/employee-record/credentials/segment-credentials-content) --# //
// #----------------------------------------------------------------------------# //

angular.module('ec-employee-segments').directive('segmentEmployeeRecordCredentialsCredentialsContent', () =>
	new CredentialsContent('credentials-content'));
