const _hlp = {
// #----------------------------------------# //
// #---- Helper (employeeAssignmentHlp) ----# //

	// demo function, please remove
	jobTypeDetailFields,
	getPayRateFrequencyString

// #-- END Helper (employeeAssignmentHlp) --# //
// #----------------------------------------# //
};

function getPayRateFrequencyString(payRateFrequency, payRateOptions){
	if(payRateFrequency == null) { return 'N/A'; }
	return payRateOptions.find(x => x.value === payRateFrequency).name;
}

function jobTypeDetailFields(){
	return [
		[{
			prop: '_fTE',
			displayText: 'FTE'
		},{
			prop: '_hoursPerDay',
			displayText: 'Hours/Day'
		},{
			prop: '_workDaysPerYear',
			displayText: 'Work Days/Year'
		}],[{
			prop: '_sickDaysPerSchoolYear',
			displayText: 'Sick Days/Year'
		},{
			prop: '_personalDaysPerSchoolYear',
			displayText: 'Personal Days/Year'
		},{
			prop: '_vacationDaysPerSchoolYear',
			displayText: 'Vacation Days/Year'
		}]
	];
}


function employeeAssignmentHlp() { return _hlp; }
angular.module('ec-employee-helpers').factory('employeeAssignmentHlp', employeeAssignmentHlp);
