/*@ngInject*/
function exportTemplateApi($http, $q, ssActiveUserSvc, ssApiResponseHlp, ssAuthSvc, buildConfig) {
	// #------------------------------------------# //
	// #---- API Endpoint (exportTemplateApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.exportTemplates;

	// export settings urls
	const typeUrl = `${baseUrl}/ExportType`;
	const baseTemplateUrl = `${baseUrl}/Organizations/:ORGID:/ExportTemplate`;
	const templateUrl = id => `${baseTemplateUrl}/${id}`;

	const templateUpdateFields = ['name', 'delimiter', 'exportSetting', 'includeColumnHeaders', 'exportFields'];

	// API endpoint methods
	return {
		deleteTemplate,
		getTemplate,
		getTemplates,
		getTypes,
		saveTemplate,
	};

	/**
	 * Gets the report templates
	 * @param {*} params
	 */
	function getTypes(params) {
		return api({ method: 'GET', url: typeUrl, params });
	}

	/**
	 * Creates a new template
	 * @param {*} template
	 */
	function createTemplate(template) {
		if (!template) { return $q.reject('template data is empty'); }

		return api({ method: 'POST', url: baseTemplateUrl, data: template });
	}

	/**
	 * Deletes a template based on the id
	 * @param {string} id
	 */
	function deleteTemplate(id) {
		if (!id) { return $q.reject('template id is empty'); }

		return api({ method: 'DELETE', url: templateUrl(id) });
	}

	/**
	 * Gets a template based on the id
	 * @param {string} id
	 * @param {*} params
	 */
	function getTemplate(id, params) {
		if (!id) { return $q.reject('template id is empty'); }
		return api({ method: 'GET', url: templateUrl(id), params });
	}

	/**
	 * Gets the templates
	 * @param {*} params
	 */
	function getTemplates(params) {
		return api({ method: 'GET', url: baseTemplateUrl, params });
	}

	/**
	 * Saves the template. Calls update or create based on whether the template is new or not.
	 * @param {*} template
	 */
	function saveTemplate(template) {
		if (!template) { return $q.reject('template data is empty'); }
		if (template.id == null) {
			return createTemplate(template);
		}
		return updateTemplate(template);
	}

	/**
	 * Updates the template
	 */
	function updateTemplate(template) {
		const patchData = templateUpdateFields.map(key => {
			return {
				op: 'replace',
				path: `/${key}`,
				value: template[key]
			};
		});

		return api({
			method: 'PATCH',
			url: templateUrl(template.id),
			data: patchData
		});
	}

	/**
	 * Base method all calls go through.
	 * @param {*} config
	 */
	function api(config) {
		var returnPromise = $q.when(ssAuthSvc.ready);

		// if the url uses the orgId, wait for it to be ready
		if (usesOrgId(config.url)) {
			returnPromise = returnPromise.then(() => ssActiveUserSvc.ready);
		}

		returnPromise = returnPromise.then(function () {
			const orgId = ssActiveUserSvc.getOrgIdSync();
			const urlNeedsOrgId = config.url.includes(':ORGID:');

			// if this url needs an orgId replacement, and we don't have an org context... abort!
			if (urlNeedsOrgId && orgId == null) { return; }
			// if this url needs an orgId, replace it
			if (urlNeedsOrgId) {
				config.url = config.url.replace(/:ORGID:/ig, orgId);
			}

			return $http(config)
				.then(res => (res.status >= 400 && res.status < 500 ? $q.reject(res) : res))
				.catch(res => {
					if (res.status === -1) { return; } // timeout or cancelled
					ssApiResponseHlp.handle4XX(res);
				});
		});

		return returnPromise;
	}

	/**
	 *
	 * @param {*} url
	 */
	function usesOrgId(url) {
		const str = (typeof url === 'function' ? url({}, {}) : url);
		if (typeof str === 'string') {
			return str.toLowerCase().includes(':orgid:');
		}
		return false;
	}

	// #-- END API Endpoint (exportTemplateApi) --# //
	// #------------------------------------------# //
}

angular.module('ec-employee-apis').factory('exportTemplateApi', exportTemplateApi);
