function endorsementFactoryHlp() {
	var baseEndorsement = {
		_code: 200,
		_externalId: null,
		_from: new Date(),
		_endorsementId: '',
		_to: null
	};

	var factory = {};

	factory.create = function () {
		return angular.extend({}, baseEndorsement);
	};

	return factory;
}

angular.module('ec-employee-helpers').factory('endorsementFactoryHlp', endorsementFactoryHlp);
