import { Segment } from 'base-segment';
// #----------------------------------------------------# //
// #---- Segment (/employee-record/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $state, $scope) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;

		// put these objects on the scope so children can access them
		$scope.employeeVm = routeVm;
		const employee = $scope.employee = $scope.employeeVm.emp;

		// hookup segment logic
		const logic = $injector.get('segmentEmployeeRecordContentLogic');

		// view model for tracking state
		const vm = $scope.vm = {
			canViewEmploymentTab: logic.canViewEmploymentTab(employee.$permitted.props),
			canViewMedicalFiles: logic.canViewMedicalFiles(employee.$permitted.props),
			canViewPersonnelTab: logic.canViewPersonnelTab(employee.$permitted.props),
			canViewCredentialTab: logic.canViewCredentialTab(employee.$permitted.props),
			canViewEmergencyTab: logic.canViewEmergencyTab(employee.$permitted.props)
		};

		if ($state.is('base.employeeRecord.employment') && !vm.canViewEmploymentTab) { $state.go('base.employeeRecord.personal'); }
		if ($state.is('base.employeeRecord.medical') && !vm.canViewMedicalFiles) { $state.go('base.employeeRecord.personal'); }
		if ($state.is('base.employeeRecord.personnel') && !vm.canViewPersonnelTab) { $state.go('base.employeeRecord.personal'); }
		if ($state.is('base.employeeRecord.credentials') && !vm.canViewCredentialTab) { $state.go('base.employeeRecord.personal'); }
		if ($state.is('base.employeeRecord.emergency') && !vm.canViewEmergencyTab) { $state.go('base.employeeRecord.personal'); }

		// behavior that needs to be bound to the view
		const go = $scope.go = {} // jshint ignore:line
	}
}

// #-- END Segment (/employee-record/segment-content) --# //
// #----------------------------------------------------# //

angular.module('ec-employee-segments').directive('segmentEmployeeRecordContent', () => new Content('content'));
