/*@ngInject*/
function logic() {
	// logic api
	return {
		getName
	};

	function getName(emp) {
		var name = emp._firstName;
		if (emp._lastName) {
			name += ' ' + emp._lastName;
		}
		return name;
	}
}
angular.module('ec-employee-segment-logic').factory('segmentEmployeeRecordHeaderLogic', logic);
