export class State {
	constructor(stateConfig, routeVm) {
		Object.assign(this, stateConfig);
		this.resolve = this.resolve || {};
		this.resolve.routeVm = () => {
			return routeVm;
		};
		/*@ngInject*/
		this.controller = ($scope, $window, routeVm) => {
			$scope.routeVm = routeVm;
		};
	}
}
