import { Component } from 'base-component';
// #-------------------------------------# //
// #---- Component (cmp-phone-group) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	emp: '=',
	editMode: '=?',
	permissions: '=',
};

class PhoneGroup extends Component {
	/*@ngInject*/
	controller($scope) {
		// view model for tracking state
		const vm = $scope.vm = {}; // jshint ignore:line

		if ($scope.emp._phoneNumbers != null) {
			$scope.emp._phoneNumbers.forEach(p => p.editable = false);
		}

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			add() {
				$scope.emp._phoneNumbers.push({
					_isPrimary: false,
					_extension: null,
					_number: null,
					_phoneType: null,
					_usageType: null,
					editable: true,
				});
			},
			onRemove(index) {
				$scope.emp._phoneNumbers.splice(index, 1);
			},
			onCheck(index) {
				$scope.emp._phoneNumbers.forEach((phone, idx) => {
					if (index === idx) { return; }
					phone._isPrimary = false;
				});
			},
		};
	}
}

// #-- END Component (cmp-phone-group) --# //
// #-------------------------------------# //

angular.module('ec-employee-components').directive('cmpPhoneGroup', () => new PhoneGroup(props, 'phone-group'));
