import { Segment } from 'base-segment';
// #----------------------------------------------------------------------# //
// #---- Segment (/employee-record/personal/segment-personal-content) ----# //

class PersonalContent extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $stateParams, ssEventSvc) {

		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentEmployeeRecordPersonalPersonalContentLogic'); // jshint ignore:line

		const vm = $scope.vm = { // jshint ignore:line
			employeeId: $stateParams.employeeId,
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			onAfterSave() {
				// emit here for sidebar changes to get picked up
				ssEventSvc.emit('module.employee.saved');
			},
		};
	}
}

// #-- END Segment (/employee-record/personal/segment-personal-content) --# //
// #----------------------------------------------------------------------# //

angular.module('ec-employee-segments').directive('segmentEmployeeRecordPersonalPersonalContent', () => new PersonalContent('personal-content'));
