import { Segment } from 'base-segment';
// #--------------------------------------------------------------------------# //
// #---- Segment (/employee-record/employment/segment-employment-content) ----# //

class EmploymentContent extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $stateParams, ssFlagSvc, ssEventSvc, ssSaveInterrupterSvc) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentEmployeeRecordEmploymentEmploymentContentLogic'); // jshint ignore:line

		const vm = $scope.vm = { // jshint ignore:line
			employeeId: $stateParams.employeeId,
		};

		ssFlagSvc.ready.then(() => {
			if (ssFlagSvc.checkState('fc-enable-save-interrupter')) {
				ssSaveInterrupterSvc.clear();
			}
		});

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			onAfterSave() {
				// emit here for sidebar changes to get picked up
				ssEventSvc.emit('module.employee.saved');
			},
		};
	}
}

// #-- END Segment (/employee-record/employment/segment-employment-content) --# //
// #--------------------------------------------------------------------------# //
angular.module('ec-employee-segments').directive('segmentEmployeeRecordEmploymentEmploymentContent', () => new EmploymentContent('employment-content'));
