import { Segment } from 'base-segment';
// #---------------------------------------------------------------# //
// #---- Segment (/reports/assignments/report/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($rootScope, $injector, $scope, $location, $window, ssEmployeeApi, ssDebounce, ssAsync, ssEventSvc, ssMultiInitiativeFeatureDetectorSvc, empReportsApi, reportHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentReportsAssignmentsContentLogic'); // jshint ignore:line

		const pageSize = 50;

		let currentRequest = null;
		

		// view model for tracking state
		const vm = $scope.vm = {
			loading: true,
			empAssignments: [],
			useGroupByEmployee: false, // true to use different display style in grid

			// changes to this object will result in data change
			filters: {},

			pages: 0,

			isGlobalDirectory: ssMultiInitiativeFeatureDetectorSvc.isDataIntegrationsEnabled(),

			filterConfig: {
				countLabel: 'Assignments of Employee',
				searchLabel: 'Employee',
				useFilter: ['jobTypes', 'departments', 'locations', 'empGroups', 'empStatus', 'empStatusReasons', 'effectiveDate', 'lastUpdated'],
				sortOptions: [{
					name: 'Last Name A-Z',
					ariaLabel: 'Sort report by last name ascending',
					value: {
						field: reportHlp.sortKeys.LAST_NAME,
						dir: 'asc'
					},
				}, {
					name: 'Last Name Z-A',
					ariaLabel: 'Sort report by last name descending',
					value: {
						field: reportHlp.sortKeys.LAST_NAME,
						dir: 'desc'
					},
				}, {
					name: 'Assignment Start Date',
					ariaLabel: 'Sort report by the assignment start date',
					value: {
						field: reportHlp.sortKeys.ASSIGNMENT_START_DATE,
						dir: 'asc'
					},
				}, {
					name: 'Location A-Z',
					ariaLabel: 'Sort report by location ascending',
					value: {
						field: reportHlp.sortKeys.LOCATION_NAME,
						dir: 'asc'
					},
				}, {
					name: 'Location Z-A',
					ariaLabel: 'Sort report by location descending',
					value: {
						field: reportHlp.sortKeys.LOCATION_NAME,
						dir: 'desc'
					},
				}, {
					name: 'Position Name',
					ariaLabel: 'Sort report by position name',
					value: {
						field: reportHlp.sortKeys.POSITION_NAME,
						dir: 'asc'
					},
				}, {
					name: 'Job Type A-Z',
					ariaLabel: 'Sort report by job type ascending',
					value: {
						field: reportHlp.sortKeys.JOB_TYPE,
						dir: 'asc'
					},
				}, {
					name: 'Job Type Z-A',
					ariaLabel: 'Sort report by job type descending',
					value: {
						field: reportHlp.sortKeys.JOB_TYPE,
						dir: 'desc'
					},
				}],
				defaultSortIndex: 0,

				exportFileName: 'employeeAssignmentExport.csv',
				export: empReportsApi.exportAssignmentsCsv,
			},
		};

		const debounce = ssDebounce((nv, ov) => {
			if (nv === ov) { return; }
			go.filterResults({ currentPage: 1 });
		}, 350);

		$scope.$watch('vm.filters', debounce, true);

		ssEventSvc.on('location-change-start', (event, toUrl) => {
			if (toUrl.indexOf('today') > -1) {
				event.preventDefault();
				$window.location = toUrl;
			}
		}, $rootScope);

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			filterResults,

			/**
			 * For DIP enabled orgs get the employee org person id and then go to the global directory
			 * @param {String} empId
			 */
			toProfile(empId) {
				if (!vm.isGlobalDirectory) { return; }
				ssEmployeeApi.get({ id: empId, fields: 'orgPersonId' }, null, false).then(emp => $location.url(`/ec-directory/person-detail/${emp._orgPersonId}/employment`));
			},
		};

		function filterResults(event) {
			vm.currentPage = event.currentPage;
			if (currentRequest != null) {
				currentRequest.$abort();
				currentRequest = null;
			}
			currentRequest = ssAsync(function*() {
				vm.loading = true;

				try {
					let queryString = Object.assign({}, {
						organizationId: routeVm.org.id
					});

					let body = Object.assign({},{
						offset: (event.currentPage - 1) * pageSize,
						limit: pageSize,
						filter:{
							lastupdatedstart: vm.filters["filter[lastUpdatedStart]"],
							lastupdatedend: vm.filters["filter[lastUpdatedEnd]"],
							statusreason: vm.filters["filter[statusReason]"],
							asoftimeutc: vm.filters["filter[asOfTimeUtc]"],
							department: vm.filters["filter[department]"],
							employmentgroup: vm.filters["filter[employmentGroup]"],
							jobtype: vm.filters["filter[jobType]"],
							location: vm.filters["filter[location]"],
							status: vm.filters["filter[status]"],
							fullname: vm.filters["filter[fullname]"]

						},	
						sort:{
							positionname: vm.filters["sort[positionName]"],
							lastname: vm.filters["sort[lastname]"],
							locationname: vm.filters["sort[locationName]"],
							assignmentstartdateutc: vm.filters["sort[assignmentStartDateUtc]"]
						}
					});

					if (!completeLastUpdatedRange()) {
						return;
					}

					// sets the filters in the querystring to make the page linkable
					reportHlp.setUrl($location, queryString);

					vm.empAssignments = yield empReportsApi.getAssignmentsPost(queryString, body);

					if (vm.empAssignments != null) {
						// set up pagination fields
						vm.filterConfig.totalResults = parseInt(vm.empAssignments.$headers['total-results'], 10);
						vm.filterConfig.totalResults = isNaN(vm.filterConfig.totalResults) ? 0 : vm.filterConfig.totalResults;
						vm.pages = Math.ceil(vm.filterConfig.totalResults / pageSize);

						vm.useGroupByEmployee = body.sort.lastname != null;
						if (vm.useGroupByEmployee) {
							vm.empAssignments.forEach(assignment => {
								assignment._employee = {
									id: assignment._employeeId,
									firstName: assignment._employeeFirstName,
									lastName: assignment._employeeLastName
								};
							});
							vm.empAssignments = reportHlp.groupByEmployee(vm.empAssignments);
						}
					} else {
						vm.filterConfig.totalResults = 0;
						vm.pages = 0;
					}
				} catch (error) {
					console && console.error && console.error('Error getting assignments', error);
				} finally {
					vm.loading = false;
				}
			})();
		}

		function completeLastUpdatedRange() {
			if (vm.filters['filter[lastUpdatedStart]'] && vm.filters['filter[lastUpdatedEnd]'] == null) {
				return false;
			}

			if (vm.filters['filter[lastUpdatedEnd]'] && vm.filters['filter[lastUpdatedStart]'] == null) {
				return false;
			}

			return true;
		}
	}
}

// #-- END Segment (/reports/assignments/report/segment-content) --# //
// #---------------------------------------------------------------# //

angular.module('ec-employee-segments').directive('segmentReportsAssignmentsReportContent', () => new Content('content'));
