import { Segment } from 'base-segment';
// #--------------------------------------------------------# //
// #---- Segment (/reports/credentials/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $location, ssEmployeeApi, ssDebounce, ssAsync, ssMultiInitiativeFeatureDetectorSvc, ssCredentialBusinessEngineSvc, empReportsApi, reportHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentReportsCredentialsContentLogic');

		const credentialConstants = ssCredentialBusinessEngineSvc.credentialConstants;
		const pageSize = 50;

		let org = routeVm.org;

		let currentRequest = null;

		// view model for tracking state
		const vm = $scope.vm = {
			loading: true,

			empCredentials: [],
			useGroupByEmployee: false, // true to use different display style in grid

			// changes to this object will result in data change
			filters: {},

			pages: 0,

			isGlobalDirectory: ssMultiInitiativeFeatureDetectorSvc.isDataIntegrationsEnabled(),

			filterConfig: {
				countLabel: 'Credentials of Employee',
				searchLabel: 'Employee',
				useFilter: ['jobTypes', 'credentials', 'empStatus', 'adminVerified', 'expiration'],
				sortOptions: [{
					name: 'Last Name A-Z',
					ariaLabel: 'Sort report by last name ascending',
					value: {
						field: reportHlp.sortKeys.LAST_NAME,
						dir: 'asc'
					},
				}, {
					name: 'Last Name Z-A',
					ariaLabel: 'Sort report by last name descending',
					value: {
						field: reportHlp.sortKeys.LAST_NAME,
						dir: 'desc'
					},
				}, {
					name: 'Expiration Date',
					ariaLabel: 'Sort report by expiration date',
					value: {
						field: reportHlp.sortKeys.EXPIRATION_DAY,
						dir: 'asc'
					},
				}],
				defaultSortIndex: 2,

				exportFileName: 'employeeCredentialsExport.csv',
				export: empReportsApi.exportCredentialsCsv,
			},
		};

		const debounce = ssDebounce(() => {
			// does not trigger on load but will as soon as the filter-bar touches this object
			go.filterResults({ currentPage: 1 });
		}, 350);

		$scope.$watch('vm.filters', debounce, true);

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			filterResults,

			/**
			 * For DIP enabled orgs get the employee org person id and then go to the global directory
			 * @param {String} empId
			 */
			toProfile(empId) {
				if (!vm.isGlobalDirectory) { return; }
				ssEmployeeApi.get({ id: empId, fields: 'orgPersonId' }, null, false).then(emp => $location.url(`/ec-directory/person-detail/${emp._orgPersonId}/credentials`));
			},

			/**
			 * Is the credential active and have active dates?
			 */
			hasActiveDates(cred) {
				let status = ssCredentialBusinessEngineSvc.getCredentialStatus(cred, moment());
				return (status.value === credentialConstants.ACTIVE || status.value === credentialConstants.NOT_ACTIVE) && (cred._stateStartedDay || cred._expirationDay || cred._verifiedDay);
			},

			/**
			 * Is the credential active and have in progress dates?
			 */
			hasInProgressDates(cred) {
				let status = ssCredentialBusinessEngineSvc.getCredentialStatus(cred, moment());
				return status.value === credentialConstants.IN_PROGRESS;
			},

			/**
			 * Is the credential a renewal?
			 */
			isRenewed(cred) {
				return cred._issuedReason === credentialConstants.CREDENTIAL_RENEWED;
			},
		};

		function filterResults(event) {
			vm.currentPage = event.currentPage;
			if (currentRequest != null) {
				currentRequest.$abort();
				currentRequest = null;
			}
			currentRequest = ssAsync(function*() {
				vm.loading = true;

				try {
					let queryString = Object.assign({}, {
						organizationId: routeVm.org.id
					});

					let body = Object.assign({}, {
						offset: (event.currentPage - 1) * pageSize,
						limit: pageSize,
						filter:{
							credentialids: vm.filters["filter[credentialIds]"],
							jobtype: vm.filters["filter[jobType]"],
							expiresafter: vm.filters["filter[expiresAfter]"],
							expiresbefore: vm.filters["filter[expiresBefore]"],
							isadminverified: JSON.stringify(vm.filters["filter[isAdminVerified]"]),
							status: vm.filters["filter[status]"],
							fullname: vm.filters["filter[fullname]"],
							"credentialtype|issuedreasons": JSON.stringify(vm.filters["filter[credentialType|IssuedReasons]"])	
						},	
						sort:{
							expirationday: vm.filters["sort[expirationDay]"],
							lastname: vm.filters["sort[lastname]"]
						}
					});

					// sets the filters in the querystring to make the page linkable
					reportHlp.setUrl($location, queryString);

					vm.empCredentials = yield empReportsApi.getCredentialsPost(queryString, body);
					if (vm.empCredentials != null) {
						vm.empCredentials.forEach(cred => logic.processCredential(cred, org));
						// set up pagination fields
						vm.filterConfig.totalResults = parseInt(vm.empCredentials.$headers['total-results'], 10);
						vm.filterConfig.totalResults = isNaN(vm.filterConfig.totalResults) ? 0 : vm.filterConfig.totalResults;
						vm.pages = Math.ceil(vm.filterConfig.totalResults / pageSize);

						vm.useGroupByEmployee = body.sort.lastname != null;
						if (vm.useGroupByEmployee) {
							vm.empCredentials = reportHlp.groupByEmployee(vm.empCredentials);
						}
					} else {
						vm.filterConfig.totalResults = 0;
						vm.pages = 0;
					}
				} catch (error) {
					console && console.error && console.error('Error getting credentials', error);
				} finally {
					vm.loading = false;
				}
			})();
		}
	}
}

// #-- END Segment (/reports/credentials/segment-content) --# //
// #--------------------------------------------------------# //

angular.module('ec-employee-segments').directive('segmentReportsCredentialsContent', () => new Content('content'));
