import { Component } from 'base-component';
// #-------------------------------------------# //
// #---- Component (cmp-report-filter-bar) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	config: '=',
	filters: '=',
	org: '=',
	exportType: '@',
};
/*
props.config = {
	export:Function - API for the export endpoint; if null export button is hidden
	totalResults:Number
	countLabel:String
	searchLabel:String
	sortOptions:Array
	defaultSortIndex:Number
	useFilter:Array
		adminVerified
		asOf
		credentials
		departments
		effectiveDate
		empGroups
		empStatus
		empStatusReasons
		expiration
		jobTypes
		lastUpdated
		locations
}
*/
class ReportFilterBar extends Component {
	/*@ngInject*/
	controller($scope, $location, $rootScope, ssAsync, ssObjectHlp, ssDepartmentSvc, ssJobTypeSvc, ssLocationSvc, ssListSvc, ssToastHlp,
		ssCredentialBusinessEngineSvc, ssCredentialDataAccessSvc, ssDateByOrgHlp, ssModalSvc, reportHlp, sortFilterHlp) {

		const credentialConstants = ssCredentialBusinessEngineSvc.credentialConstants;

		// default to expiration date
		const orgFilter = sortFilterHlp.searchFilter($scope.org.id, reportHlp.filtersKeys.ORGANIZATION_ID);

		const config = $scope.config || {};

		let selectedSortKey;

		// view model for tracking state
		const vm = $scope.vm = {
			filters: {},
			invalidLastUpdated: false,
			exportTemplates: null
		};

		const exportTemplates = $rootScope.utils.findInParentScope('exportTemplates', $scope);

		if (exportTemplates && exportTemplates.length) {
			vm.exportTemplates = exportTemplates.map(t => {
				return {
					name: t.name,
					value: t.id
				};
			});
			vm.exportTemplates.unshift({ name: 'Default CSV', value: null });
		}

		// fill data
		if (useFilter('empStatus')) {
			vm.statuses = reportHlp.statusOptions;
		}
		if (useFilter('adminVerified')) {
			vm.adminVerified = reportHlp.adminVerifiedOptions;
		}

		// then set defaults as some of the defaults may use the data
		setDefaults();

		// add org filter
		$scope.filters = Object.assign({}, $scope.filters, orgFilter);

		watchers();

		// Prepare the data for the dropdowns
		ssAsync(function*() {
			try {
				if (useFilter('departments')) {
					vm.departments = yield ssDepartmentSvc.getAll();
				}
				if (useFilter('jobTypes')) {
					vm.jobTypes = yield ssJobTypeSvc.getAll();
				}
				if (useFilter('locations')) {
					vm.locations = yield ssLocationSvc.getAll();
				}
				if (useFilter('empGroups')) {
					vm.empGroups = yield ssListSvc.getByExternalId('jobtype.employmentgroups');
					// set _name; the super-multi-filter-list expects it
					vm.empGroups.forEach(g => g._name = g.name);
				}
				if (useFilter('empStatusReasons')) {
					vm.empStatusReasons = yield ssListSvc.getByExternalId('employee.statuschangereasons');
					// set _name; the super-multi-filter-list expects it
					vm.empStatusReasons.forEach(g => g._name = g.name);
				}

				vm.sortOptions = config.sortOptions;

				// passing in vm as 'data'
				let selected = reportHlp.getSelectedFromUrl($location.search(), vm);

				// set the values on the
				Object.keys(selected).forEach(k => vm.filters[k] = selected[k]);
			} catch (e) {
				console && console.error && console.error('Error getting data', e);
			}
		})();

		if (useFilter('credentials')) {
			// Prepare the credential data for the dropdowns
			ssAsync(function*() {
				vm.loadingCreds = true;
				try {
					let subtypes = [credentialConstants.CREDENTIAL_NEW, credentialConstants.CREDENTIAL_RENEWED, credentialConstants.CREDENTIAL_IN_PROGRESS];
					let subtypesName = {};
					subtypesName[credentialConstants.CREDENTIAL_NEW] = 'Initial';
					subtypesName[credentialConstants.CREDENTIAL_RENEWED] = 'Renewal';
					subtypesName[credentialConstants.CREDENTIAL_IN_PROGRESS] = 'In Progress';

					vm.credTypes = yield ssCredentialDataAccessSvc.getCredentialTypes();

					vm.credTypes.forEach(t => {
						t.subTypes = subtypes.map(st => {
							return {
								_name: subtypesName[st],
								value: {
									typeId: t.id,
									subtypeId: st,
								},
								pillText: `${subtypesName[st]} - ${t._name}`,
							};
						});
					});

					let certIssuers = yield ssCredentialDataAccessSvc.getIssuersByCredentialType(credentialConstants.CERTIFICATE_LICENSE, credentialConstants.STATE_NATIONAL);
					let clearIssuers = yield ssCredentialDataAccessSvc.getIssuersByCredentialType(credentialConstants.CLEARANCES, credentialConstants.STATE_NATIONAL);
					let stateDmvIssuers = yield ssCredentialDataAccessSvc.getIssuersByCredentialType(credentialConstants.DRIVER_LICENSE, credentialConstants.STATE_NATIONAL);

					let stateIssuers = [...certIssuers, ...clearIssuers, ...stateDmvIssuers];
					let customIssuers = yield ssCredentialDataAccessSvc.getIssuers(credentialConstants.DISTRICT_CUSTOM);

					let stateCreds = yield ssCredentialDataAccessSvc.getMany();

					let orgCreds = yield ssCredentialDataAccessSvc.getMany(sortFilterHlp.searchFilter($scope.org.id, 'organizationId'));
					vm.credentials = [...stateCreds, ...orgCreds];

					// get state
					vm.credentials.forEach(c => {
						// try state/national issuer
						let issuer = stateIssuers.find(i => i.id === c._issuerId);
						if (issuer != null) {
							// it's state/national
							if (issuer._stateId != null) {
								c._name = `${issuer._stateId} - ${c._name}`;
								c.pillText = c._name;
								return;
							}
						} else {
							// it's custom. Try to use the org's state
							issuer = customIssuers.find(i => i.id === c._issuerId);
							if (issuer != null && ssObjectHlp.glean($scope.org, _ => _._address.state) != null) {
								c._name = `${$scope.org._address.state} - ${c._name}`;
								c.pillText = c._name;
								return;
							}
						}
						// we were not able to get the state; do nothing; should not get to this point
						// console && console.info && console.info('Cannot prefix Credential with state', c);
					});

					vm.loadingCreds = false;
				} catch (e) {
					console && console.error && console.error('Error getting data', e);
				}
			})();
		}

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			clear() {
				// clear the filters object; keep the org filter
				$scope.filters = Object.assign({}, $scope.filters, orgFilter);

				setDefaults();

				// uncheck everything
				// ****************************************************************
				if (useFilter('departments')) {
					vm.departments.forEach(item => item.checked = false);
				}
				if (useFilter('jobTypes')) {
					vm.jobTypes.forEach(item => item.checked = false);
				}
				if (useFilter('locations')) {
					vm.locations.forEach(item => item.checked = false);
				}
				if (useFilter('empGroups')) {
					vm.empGroups.forEach(item => item.checked = false);
				}
				if (useFilter('credentials')) {
					vm.credTypes.forEach(item => item.checked = false);
					vm.credentials.forEach(item => item.checked = false);
				}
				if (useFilter('empStatus')) {
					vm.statuses.forEach(item => item.checked = false);
				}
			},

			export (templateId) {
				let content = `Generate and download report for ${config.totalResults} ${config.countLabel}${config.totalResults === 1 ? '' : 's'}?`;
				var modalConfig = {
					title: '',
					content: content,
					primaryText: 'Download',
					secondaryText: 'Cancel',
					size: 'medium'
				};

				ssModalSvc.showStandard(modalConfig).then(() => exportFile(templateId));
			},

			showClear() {
				// show clear if there are any keys in the filters other than org and sorts
				let keys = Object.keys($scope.filters).filter(k => !k.startsWith('sort') && k !== `filter[${reportHlp.filtersKeys.ORGANIZATION_ID}]`);
				return keys.length > 0;
			},

			useFilter,
		};

		function setDefaults() {
			setDefaultSort();

			vm.filters.search = '';

			if (useFilter('departments')) {
				vm.filters.departments = [];
			}

			if (useFilter('jobTypes')) {
				vm.filters.jobTypes = [];
			}

			if (useFilter('locations')) {
				vm.filters.locations = [];
			}

			if (useFilter('empGroups')) {
				vm.filters.empGroups = [];
			}

			if (useFilter('credentials')) {
				vm.filters.credTypes = [];
				vm.filters.credentials = [];
			}

			if (useFilter('empStatus')) {
				vm.filters.statuses = [];
			}

			if (useFilter('empStatusReasons')) {
				vm.filters.empStatusReasons = [];
			}

			if (useFilter('adminVerified')) {
				// default to 'All'
				vm.filters.adminVerified = vm.adminVerified[0].value;
			}

			if (useFilter('expiration')) {
				vm.filters.expiresAfter = '';
				vm.filters.expiresBefore = '';
			}

			if (useFilter('effectiveDate')) {
				vm.filters.effectiveDate = '';
			}

			if (useFilter('lastUpdated')) {
				vm.filters.lastUpdatedStart = '';
				vm.filters.lastUpdatedEnd = '';
			}
		}

		function setDefaultSort() {
			if (config.sortOptions == null || !config.sortOptions.length) { return; }

			if (typeof config.defaultSortIndex !== 'number') {
				config.defaultSortIndex = 0;
			}
			let defaultSortType = config.sortOptions[config.defaultSortIndex];
			vm.filters.sortBy = defaultSortType.value;
			selectedSortKey = defaultSortType.value.field;
		}

		function useFilter(filter) {
			if (ssObjectHlp.glean(config, _ => _.useFilter) != null && config.useFilter.length) {
				return config.useFilter.includes(filter);
			}
			return false;
		}

		function watchers() {
			$scope.$watch('vm.exportOptionValue', nv => {
				if (nv === 'defaultCSV') {
					go.export();
				}
			});

			$scope.$watch('vm.filters.search', nv => {
				if (typeof nv === 'string' && nv.trim().length) {
					$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.searchFilter(nv.trim(), reportHlp.filtersKeys.FULL_NAME));
				} else {
					delete $scope.filters[`filter[${reportHlp.filtersKeys.FULL_NAME}]`];
				}
			});

			if (useFilter('departments')) {
				$scope.$watch('vm.filters.departments.length', nv => {
					if (nv == 0) {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.DEPARTMENT}]`];
					} else {
						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.filterOrNull(vm.filters.departments, reportHlp.filtersKeys.DEPARTMENT));
					}
				});
			}

			if (useFilter('jobTypes')) {
				$scope.$watch('vm.filters.jobTypes.length', nv => {
					if (nv == 0) {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.JOB_TYPE}]`];
					} else {
						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.filterOrNull(vm.filters.jobTypes, reportHlp.filtersKeys.JOB_TYPE));
					}
				});
			}

			if (useFilter('locations')) {
				$scope.$watch('vm.filters.locations.length', nv => {
					if (nv == 0) {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.LOCATION}]`];
					} else {
						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.filterOrNull(vm.filters.locations, reportHlp.filtersKeys.LOCATION));
					}
				});
			}

			if (useFilter('empGroups')) {
				$scope.$watch('vm.filters.empGroups.length', nv => {
					if (nv == 0) {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.EMPLOYMENT_GROUP}]`];
					} else {
						let empGroupById = vm.filters.empGroups.map(item => {
							return {
								id: item.value
							};
						});
						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.filterOrNull(empGroupById, reportHlp.filtersKeys.EMPLOYMENT_GROUP));
					}
				});
			}

			if (useFilter('credentials')) {
				$scope.$watch('vm.filters.credTypes.length', nv => {
					if (nv == 0) {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.CRED_TYPES}]`];
					} else {

						let selectedTypes = {};
						vm.filters.credTypes.forEach(t => {
							let typeId = t.value.typeId;
							if (selectedTypes[`${typeId}`] == null) {
								selectedTypes[`${typeId}`] = [];
							}
							selectedTypes[`${typeId}`].push(t.value.subtypeId);
						});

						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.searchFilter(selectedTypes, reportHlp.filtersKeys.CRED_TYPES));
					}
				});

				$scope.$watch('vm.filters.credentials.length', nv => {
					if (nv == 0) {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.CREDENTIAL_IDS}]`];
					} else {
						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.filterOrNull(vm.filters.credentials, reportHlp.filtersKeys.CREDENTIAL_IDS));
					}
				});
			}

			if (useFilter('empStatus')) {
				$scope.$watch('vm.filters.statuses.length', nv => {
					if (nv == 0) {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.STATUS}]`];
					} else {
						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.filterOrNull(vm.filters.statuses, reportHlp.filtersKeys.STATUS));
					}
				});
			}

			if (useFilter('empStatusReasons')) {
				$scope.$watch('vm.filters.empStatusReasons.length', nv => {
					if (nv == 0) {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.STATUS_REASON}]`];
					} else {
						let empStatusReasonsById = vm.filters.empStatusReasons.map(item => {
							return {
								id: item.value
							};
						});
						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.filterOrNull(empStatusReasonsById, reportHlp.filtersKeys.STATUS_REASON));
					}
				});
			}

			if (useFilter('adminVerified')) {
				$scope.$watch('vm.filters.adminVerified', nv => {
					if (nv == null) {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.ADMIN_VERIFIED}]`];
					} else {
						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.searchFilter(nv, reportHlp.filtersKeys.ADMIN_VERIFIED));
					}
				});
			}

			if (useFilter('expiration')) {
				$scope.$watch('vm.filters.expiresAfter', nv => {
					if (nv == null || nv === '') {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.EXPIRES_AFTER}]`];
					} else {
						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.searchFilter(moment(nv).format('YYYY-MM-DD'), reportHlp.filtersKeys.EXPIRES_AFTER));
					}
				});
				$scope.$watch('vm.filters.expiresBefore', nv => {
					if (nv == null || nv === '') {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.EXPIRES_BEFORE}]`];
					} else {
						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.searchFilter(moment(nv).format('YYYY-MM-DD'), reportHlp.filtersKeys.EXPIRES_BEFORE));
					}
				});
			}

			if (useFilter('effectiveDate')) {
				$scope.$watch('vm.filters.effectiveDate', nv => {
					if (nv == null || nv === '') {
						nv = moment().toISOString();
					}
					
					let lastUpdatedStart = ssDateByOrgHlp.startOfDayRelativeTo(moment(nv).startOf('day'), $scope.org);

					$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.searchFilter(lastUpdatedStart, reportHlp.filtersKeys.EFFECTIVE_DATE));
				});
			}

			if (useFilter('lastUpdated')) {
				$scope.$watch('vm.filters.lastUpdatedStart', nv => {
					if (nv == null || nv === '') {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.LAST_UPDATED_START}]`];
						vm.filters.lastUpdatedEnd = '';
					} else {
						let lastUpdatedStart = ssDateByOrgHlp.startOfDayRelativeTo(moment(nv), $scope.org);
						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.searchFilter(lastUpdatedStart, reportHlp.filtersKeys.LAST_UPDATED_START));
					}

					validateRange();
				});
				$scope.$watch('vm.filters.lastUpdatedEnd', nv => {
					if (nv == null || nv === '') {
						delete $scope.filters[`filter[${reportHlp.filtersKeys.LAST_UPDATED_END}]`];
					} else {
						let lastUpdatedEnd = ssDateByOrgHlp.endOfDayRelativeTo(moment(nv), $scope.org);
						$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.searchFilter(lastUpdatedEnd, reportHlp.filtersKeys.LAST_UPDATED_END));
					}

					validateRange();
				});
			}

			if (config.sortOptions) {
				$scope.$watch('vm.filters.sortBy', nv => {
					// set the default sort if nothing came in
					if (nv == null) { setDefaultSort(); }

					// delete the existing sort
					delete $scope.filters[`sort[${selectedSortKey}]`];

					$scope.filters = Object.assign({}, $scope.filters, sortFilterHlp.sortOrNull(vm.filters.sortBy));

					// set the selected key
					selectedSortKey = vm.filters.sortBy.field;
				});
			}
		}

		function exportFile(templateId) {
			let exportFileName = 'report.csv';
			if (config.exportFileName == null) {
				exportFileName = config.exportFileName;
			}
			// Export Credentials to CSV
			var f = new FileReader();
			f.addEventListener('load', () => {
				f.removeEventListener('load', this, true);
				saveLocalExport(f.result, exportFileName);
			}, false);

			// Get the CSV as post data and push it into a FileReader
			let filters = Object.assign({}, {
				offset: 0,
				limit: 9999999,
				organizationId: $scope.org.id
			}, $scope.filters);

			vm.isExporting = true;
			return ssAsync(function*() {
				let response = yield config.export(templateId, reportHlp.toQuerystring(filters));

				if (response && response.data) {
					// Create the dummy anchor and click it to start the download.
					var a = document.createElement("a");
					a.style.display = 'none';
					a.href = response.data.data.attributes.string;

					document.body.appendChild(a);
					a.click();
					document.body.removeChild(a);
				} else {
					console && console.error && console.error('Invalid response from employee credential endpoint. Unable to save to csv.');
					ssToastHlp.error('Unable to save export', 'Error', false);
				}

				vm.isExporting = false;
			})();
		}

		function validateRange() {
			if (vm.filters.lastUpdatedStart == null || vm.filters.lastUpdatedStart === '') {
				vm.invalidLastUpdated = false;
				return;
			}
			if (vm.filters.lastUpdatedEnd == null || vm.filters.lastUpdatedEnd === '') {
				vm.invalidLastUpdated = false;
				return;
			}

			if (vm.filters.lastUpdatedStart > vm.filters.lastUpdatedEnd) {
				vm.invalidLastUpdated = true;
			} else {
				vm.invalidLastUpdated = false;
			}

		}
	}
}

// #-- END Component (cmp-report-filter-bar) --# //
// #-------------------------------------------# //

angular.module('ec-employee-components').directive('cmpReportFilterBar', () => new ReportFilterBar(props, 'report-filter-bar'));
