const _hlp = {
	// #--------------------------------# //
	// #---- Helper (sortFilterHlp) ----# //

	/**
	 *
	 * @param {Date} date - a date object
	 * @param {string} type - the filter parameter name that the server will use to build it's search
 	 * @returns {string?} returns the filter object for the specified date object / field type (OR NULL)
	 */
	filterDateOrNull(date, type) {
		if (date && date != '') {
			var o = {};

			o['filter[' + type + ']'] = date;

			return o;
		} else {
			return null;
		}
	},

	/**
	 * filterOrNull : pulls a super-multi-filter-list's options into a filter object
	 * 								that can be used by the data call
	 * @param {array} filtersList - the list of options to go through
	 * @param {string} type - the filter parameter name that the server will use to build it's search
	 * @returns {object?} - the filter object representing the super-multi-filter-list's selected options (OR NULL)
	 */
	filterOrNull(filtersList, type) {
		if (filtersList.length > 0) {
			var o = {};

			o['filter[' + type + ']'] = filtersList.map((item) => {
				return item.id;
			}).join(',');

			return o;
		} else {
			return null;
		}
	},

	/**
	 * sortOrNull - Sets the sort object for a filter request
	 * @param {object} sort - object describing a sort field and direction
	 * @returns {object} - the sort object for a filter request
	 */
	sortOrNull(sort) {
		var o = {};
		o['sort[' + sort.field + ']'] = sort.dir;
		return o;
	},

	/**
	 *
	 * @param {string} val - the value so search for
	 * @param {string} type - the field name that the val is mapped to
	 * @returns {object?} - the search object that will be sent to the filter request (OR NULL)
	 */
	searchFilter(val, type) {
		var o = {};

		if (val != null) {
			o['filter[' + type + ']'] = val;
			return o;
		} else {
			return null;
		}
	},
	
	/**
	 * filterOrNullForPost : pulls a super-multi-filter-list's options into a string for filtering
	 * 								that can be used by the data call
	 * @param {array} filtersList - the list of options to go through
	 * @returns {string} - Comma separated list of items to filter for.
	 */
	filterOrNullForPost(filtersList) {
		if (filtersList.length > 0) {
			var o = []

			filtersList.map((item) => {
				o.push(item.id)
			})

			return o.join(',');
		} else {
			return null;
		}
	},

	// #-- END Helper (sortFilterHlp) --# //
	// #--------------------------------# //
};

function sortFilterHlp() { return _hlp; }
angular.module('ec-employee-helpers').factory('sortFilterHlp', sortFilterHlp);
