import {Segment} from 'base-segment';
// #----------------------------------------------# //
// #---- Segment (/my-profile/segment-header) ----# //

class Header extends Segment {
	/*@ngInject*/
	controller($injector, $state, $scope, ssActiveUserSvc) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentMyProfileHeaderLogic'); // jshint ignore:line


		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
			test: 'test'
		};

		ssActiveUserSvc.getEmployee().then(user => {
			vm.fullName = user.fullName;
			$state.go('base.employeeRecord',{ module: 'ec-employee', employeeId: user.id })
		});

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			click() {
				vm.test = 'works!';
			}
		};
	}
}

// #-- END Segment (/my-profile/segment-header) --# //
// #----------------------------------------------# //

angular.module('ec-employee-segments').directive('segmentMyProfileHeader', () => new Header('header') );
