/* globals moment */
import { State } from 'base-state';
// #------------------------# //
// #---- State (/index) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {
	location: [],
	jobType: [],
	department: [],
	employmentGroup: [],
	status: [],
	fromHireDate: null,
	toHireDate: null,
	effectiveDate: moment().endOf('day').toISOString()
};

const stateName = 'index';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/',
	templateUrl: 'routes/index/index.html',
	resolve: {
		org: /*@ngInject*/ (ssOrganizationSvc, ssAsync, ssMultiInitiativeFeatureDetectorSvc) => {
			return (ssAsync(function*() {
				routeVm.org = yield ssOrganizationSvc.getActiveOrg();
				routeVm.isGlobalDirectory = ssMultiInitiativeFeatureDetectorSvc.isGlobalDirectoryEnabled();
			})());
		},
		$title: /*@ngInject*/ () => {
			return 'Frontline Central - Employees';
		}
	},
	$permissions: (permissionSvc) => permissionSvc.isPermittedAction('bundle', 'viewothers'),
};

// #-- END State (/index) --# //
// #------------------------# //

angular.module('ec-employee').config( /*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
