import { Segment } from 'base-segment';
// #--------------------------------------------------------# //
// #---- Segment (/reports/demographic/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $location, ssFlagSvc, ssPermissionsSvc, ssStorage, ngIntroService) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentReportsDemographicContentLogic'); // jshint ignore:line

		// redirect to the slash url because the automatic redirect in the state does not bring querystring params
		if ($location.path() === '/ec-employee/reports/demographic') {
			$location.path($location.path() + '/report');
		}

		// view model for tracking state
		const vm = $scope.vm = {
			templateId: null,
			showTabs: ssFlagSvc.checkState('fc-emp-ui-custom-exports'),
		};

		// intro.js based UI feature tour, used in the link function below
		$scope.ngIntroService = ngIntroService;
		$scope.ssStorage = ssStorage;
		$scope.ssFlagSvc = ssFlagSvc;
		$scope.ssPermissionsSvc = ssPermissionsSvc;

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
		};

		if (ssPermissionsSvc.isPermittedAction('exportTemplates', 'view') && vm.showTabs) {
			$scope.exportType = routeVm.exportType;
			$scope.exportTemplates = routeVm.exportTemplates;

			// dynamically create the link for the settings tab; we currently only have 1 template so use the first we find otherwise use 'create'
			vm.templateId = $scope.exportTemplates.length ? $scope.exportTemplates[0].id : 'create';

			$scope.$on('ec-employee.reports.feature.intro', function (e) {
				ssStorage.save('ec-employee.reports.feature.intro', true, { persistent: true, expires: false });
			});
		}
	}

	link(scope, elem) {
		// intro.js based UI feature tour
		function cannotSeeUITour() {
			return (
				scope.ssStorage.load('ec-employee.reports.feature.intro') ||
				!scope.ssPermissionsSvc.isPermittedAction('exportTemplates', 'view') ||
				!scope.ssFlagSvc.checkState('fc-emp-ui-custom-exports')
			);
		}

		if (cannotSeeUITour()) {
			return;
		}

		angular.element(document).ready(function() {
			scope.ngIntroService.setOptions({
				steps: [{
					element: angular.element(elem[0].querySelector('#ui-tour-step1'))[0],
					intro: '<h4>Introducing Export Settings</h4><p>You can create a custom template to manage the report output.</p>'
				}],
				doneLabel: 'Got it!',
				showBullets: false,
				showStepNumbers: false,
				disableInteraction: true
			});

			scope.ngIntroService.onExit(function () {
				scope.$emit('ec-employee.reports.feature.intro');
			});

			scope.ngIntroService.start();
		});
	}
}

// #-- END Segment (/reports/demographic/segment-content) --# //
// #--------------------------------------------------------# //

angular.module('ec-employee-segments').directive('segmentReportsDemographicContent', () => new Content('content'));
