import { State } from 'base-state';
// #--------------------------------------# //
// #---- State (/reports/assignments) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'reports.assignments';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/assignments',
	templateUrl: 'routes/reports/assignments/assignments.html',
	resolve: {
		exportType: /*@ngInject*/ (ssAsync, ssPermissionsSvc, ssFlagSvc, exportTemplateApi, sortFilterHlp) => {
			const exportTypeName = 'EmployeeAssignments';

			return ssPermissionsSvc.ready.then(() => {
				if (ssPermissionsSvc.isNotPermittedAction('exportTemplates', 'view')
					|| !ssFlagSvc.checkState('fc-emp-ui-custom-exports')) { return; }

				return ssAsync(function*() {
					// get the type and put it on the scope so children scopes can read this
					const types = yield exportTemplateApi.getTypes(Object.assign({}, sortFilterHlp.filterOrNull([{ id: exportTypeName }], 'exportSource')));
					routeVm.exportType = types.data.data[0];

					// get the templates based on the type and put it on the scope so report scope can use it to build the export button
					// dynamically create the link for the settings tab; we currently only have 1 template so use the first we find otherwise use 'create'
					const templates = yield exportTemplateApi.getTemplates(Object.assign({}, sortFilterHlp.filterOrNull([{ id: routeVm.exportType.id }], 'exportTypeId')));
					routeVm.exportTemplates = templates.data.data;
				})();
			});
		},
		$title: /*@ngInject*/ () => {
			return 'Employee Records - Assignments Report';
		}
	},
	$permissions: (permissionSvc) => {
		return permissionSvc.isPermittedAction('assignmentReports', 'view');
	},
};

// #-- END State (/reports/assignments) --# //
// #--------------------------------------# //

angular.module('ec-employee').config( /*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
