// #----------------------------# //
// #---- Helper (reportHlp) ----# //

let _hlp = null;

/*@ngInject*/
function reportHlp(moment, ssStringHlp, enumEmployeeStatus) {
	// if the object has been created already return it
	if (_hlp != null) { return _hlp; }

	const filtersKeys = {
		ADMIN_VERIFIED: 'isAdminVerified',
		CRED_TYPES: 'credentialType|IssuedReasons',
		CREDENTIAL_IDS: 'credentialIds',
		DEPARTMENT: 'department',
		EFFECTIVE_DATE: 'asOfTimeUtc',
		EMPLOYMENT_GROUP: 'employmentGroup',
		EXPIRES_AFTER: 'expiresAfter',
		EXPIRES_BEFORE: 'expiresBefore',
		FULL_NAME: 'fullname',
		JOB_TYPE: 'jobType',
		LAST_UPDATED_START: 'lastUpdatedStart',
		LAST_UPDATED_END: 'lastUpdatedEnd',
		LOCATION: 'location',
		ORGANIZATION_ID: 'organizationId',
		STATUS: 'status',
		STATUS_REASON: 'statusReason',
	};

	const sortKeys = {
		ASSIGNMENT_START_DATE: 'assignmentStartDateUtc',
		EMP_STATUS: 'employeeStatus',
		EMP_LAST_UPDATED: 'lastUpdatedUtc',
		EXPIRATION_DAY: 'expirationDay',
		HIRE_DATE: 'hireDate',
		JOB_TYPE: 'jobTypeName',
		LAST_NAME: 'lastname',
		POSITION_NAME: 'positionName',
		LOCATION_NAME: 'locationName',
	};

	const statusOptions = enumEmployeeStatus.getEnums().map(o => {
		return {
			_name: ssStringHlp.toProperCase(o.name.toLowerCase()),
			id: o.value
		};
	});

	const adminVerifiedOptions = [{
		name: 'All',
		value: null
	}, {
		name: 'Verified',
		value: true
	}, {
		name: 'Not Verified',
		value: false
	}];

	// create the object and return it immediately
	_hlp = {
		adminVerifiedOptions,
		filtersKeys,
		getDateString,
		getSelectedFromUrl,
		groupByEmployee,
		setUrl,
		sortKeys,
		statusOptions,
		toQuerystring,
	};
	return _hlp;

	/**
	 * @param {Array} results
	 */
	function groupByEmployee(results) {
		const map = {};
		results.forEach(item => {
			const emp = item._employee;
			const key = `${emp.lastName},${emp.firstName},${emp.id}`;
			const collection = map[key];
			if (!collection) {
				map[key] = [item];
			} else {
				collection.push(item);
			}
		});
		return map;
	}

	/**
	 * Sets the url based on the filter array
	 */
	function setUrl(location, filter) {
		// clear keys that are not filters
		let keys = Object.keys(location.search()).filter(key => !key.startsWith('filter'));
		keys.forEach(key => location.search(key, null));

		let skips = [`filter[${filtersKeys.ORGANIZATION_ID}]`,
			`filter[${filtersKeys.CREDENTIAL_IDS}]`,
			`filter[${filtersKeys.CRED_TYPES}]`
		];
		// sets the querystring params
		Object.keys(filter).forEach(key => {
			// skip non filters and sorts and organizationId
			if ((!key.startsWith('filter') && !key.startsWith('sort')) || skips.includes(key)) { return; }
			location.search(key, filter[key]);
		});
	}

	/**
	 *
	 * @param {*} params
	 * @param {*} data
	 * @param {*} data.sortOptions
	 * @param {*} data.departments
	 * @param {*} data.jobTypes
	 * @param {*} data.locations
	 * @param {*} data.empGroups
	 * @param {*} data.statusReasons
	 */
	function getSelectedFromUrl(params, data) {
		let result = {};

		result.sortBy = getSelectedSortFromUrl(params, data.sortOptions);

		let f = `filter[${filtersKeys.STATUS}]`;
		if (params[f]) {
			let val = params[f].split(',');
			result.statuses = statusOptions.filter(s => val.includes(`${s.id}`));
		}

		f = `filter[${filtersKeys.STATUS_REASON}]`;
		if (params[f]) {
			let val = params[f].split(',');
			result.statusReasons = data.statusReasons.filter(s => val.includes(`${s.id}`));
		}

		f = `filter[${filtersKeys.JOB_TYPE}]`;
		if (params[f]) {
			let val = params[f].split(',');
			result.jobTypes = data.jobTypes.filter(s => val.includes(`${s.id}`));
		}

		f = `filter[${filtersKeys.DEPARTMENT}]`;
		if (params[f]) {
			let val = params[f].split(',');
			result.departments = data.departments.filter(s => val.includes(`${s.id}`));
		}

		f = `filter[${filtersKeys.LOCATION}]`;
		if (params[f]) {
			let val = params[f].split(',');
			result.locations = data.locations.filter(s => val.includes(`${s.id}`));
		}

		f = `filter[${filtersKeys.EMPLOYMENT_GROUP}]`;
		if (params[f]) {
			let val = params[f].split(',');
			result.empGroups = data.empGroups.filter(s => val.includes(`${s.id}`));
		}

		f = `filter[${filtersKeys.ADMIN_VERIFIED}]`;
		if (params[f] != null) {
			result.adminVerified = params[f] === 'true' || params[f] === true;
		}

		f = `filter[${filtersKeys.FULL_NAME}]`;
		if (params[f]) {
			result.search = params[f];
		}

		f = `filter[${filtersKeys.EXPIRES_AFTER}]`;
		if (params[f]) {
			result.expiresAfter = moment(params[f]);
		}

		f = `filter[${filtersKeys.EXPIRES_BEFORE}]`;
		if (params[f]) {
			result.expiresBefore = moment(params[f]);
		}

		f = `filter[${filtersKeys.EFFECTIVE_DATE}]`;

		if (params[f]) {
			if (params[f] === 'today') {
				result.effectiveDate = moment();
			} else {
				result.effectiveDate = moment(params[f]);
			}
		}

		f = `filter[${filtersKeys.LAST_UPDATED_START}]`;
		if (params[f]) {
			result.lastUpdatedStart = moment(params[f]);
		}

		f = `filter[${filtersKeys.LAST_UPDATED_END}]`;
		if (params[f]) {
			result.lastUpdatedEnd = moment(params[f]);
		}

		return result;
	}

	/**
	 *
	 */
	function getSelectedSortFromUrl(params, sortOptions) {
		if (sortOptions == null || !sortOptions.length) { return null; }

		for (let i = 0; i < Object.keys(sortKeys).length; i++) {
			const key = Object.keys(sortKeys)[i];

			let sort = findSort(params, sortOptions, sortKeys[key]);
			if (sort == null) { continue; }
			return sort;
		}

		return null;

		/*
		Looks like:
			name: '',
			ariaLabel: '',
			value: {
				field: sortKeys.SOMETHING,
				dir: 'asc' // asc|desc
			},
		*/
		function findSort(params, sortOptions, key) {
			// do we have lastname sort?
			if (params[`sort[${key}]`]) {
				let val = params[`sort[${key}]`];
				return sortOptions.find(x => x.value.field === key && x.value.dir === val).value;
			}
			return null;
		}
	}

	/**
	 * Used in export
	 * @param {*} filter
	 */
	function toQuerystring(filter) {
		let ret = [];
		for (var key in filter) {
			if (filter.hasOwnProperty(key)) {
				if (typeof {} === typeof filter[key]) {
					ret.push(`${encodeURIComponent(key)}=${JSON.stringify(filter[key])}`);
				} else {
					ret.push(`${encodeURIComponent(key)}=${filter[key]}`);
				}
			}
		}

		return ret.join('&');
	}

	/**
	 *
	 */
	function getDateString(start = '', end = '') {
		if (start === '' && end === '') { return ''; }
		let foreverPast = start.endsWith('0001');
		let foreverFuture = end.endsWith('9999');

		if (foreverPast && foreverFuture) { return 'Current'; }

		let res = '';

		if (foreverPast) {
			res = 'Current';
		} else {
			res = start.replace(/-/gi,'/');
		}

		if (res.length && end.length) { res += ' - '; }

		if (foreverFuture) {
			res += 'Current';
		} else {
			res += end.replace(/-/gi, '/');
		}

		return res;
	}
}
// #-- END Helper (reportHlp) --# //
// #----------------------------# //
angular.module('ec-employee-helpers').factory('reportHlp', reportHlp);
