import { State } from 'base-state';
// #-----------------------------------------------# //
// #---- State (/reports/assignments/settings) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'reports.assignments.settings';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/settings/:templateId',
	templateUrl: 'routes/reports/assignments/settings/settings.html',
	resolve: {
		$title: /*@ngInject*/ () => {
			return 'Employee Assignment Report - Export Settings';
		}
	},
	$permissions: (permissionSvc) => permissionSvc.isPermittedAction('exportTemplates', 'view'),
};

// #-- END State (/reports/assignments/settings) --# //
// #-----------------------------------------------# //

angular.module('ec-employee').config( /*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
