import {
	Component
}
from 'base-component';
// #-----------------------------------------# //
// #---- Component (cmp-form-field-date) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	label: '@',
	name: '@?',
	model: '=?',
	required: '@',
	format: '@?'
};

const config = {
	transclude: true
};

class FormFieldDate extends Component {

	link(scope, elem, attrs) {
		//moved this from the template to here so that we can apply custom directives that
		// modify the markup.  Angular does not let you modify the class when there is a
		//	ng-class or an expression in the class attribute.
		elem.addClass(scope.vm.fieldClass);
	}

	/*@ngInject*/
	controller($scope, ssStringHlp) {
		// view model for tracking state
		const vm = $scope.vm = {
			format: $scope.format || 'MM/dd/yyyy'
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { //jshint ignore:line
		};

		if ($scope.label) {
			let attr = null;

			if ($scope.name) {
				attr = $scope.name;
			} else {
				attr = $scope.label;
			}

			let fieldClass = attr.replace(/\W/g, ' ');
			fieldClass = ssStringHlp.toDashCase(fieldClass);
			vm.fieldClass = fieldClass !== null ? 'field-' + fieldClass : '';

		} else {
			console.warn('Please provide at least a label attribute.');
		}
	}
}

// #-- END Component (cmp-form-field-date) --# //
// #-----------------------------------------# //

angular.module('ec-employee-components').directive('cmpFormFieldDate', () => new FormFieldDate(props,
	'form-field-date', config));
