import { Segment } from 'base-segment';
// #----------------------------------------------------# //
// #---- Segment (/employee-record/segment-sidebar) ----# //

class Sidebar extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $stateParams, ssEmployeeApi, ssOrganizationSvc, ssAsync, ssEventSvc,
			ssListSvc, ssAssignmentApi, ssJobTypeSvc, ssPositionsApi, ssDateByOrgHlp, enumEmployeeStatus) {

		// view model that is shared by all segments on this route, used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line
		let org = null;

		// hookup segment logic
		const logic = $injector.get('segmentEmployeeRecordSidebarLogic'); // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = {
			loading: true
		};

		load();

		// employee is updated, update the view
		ssEventSvc.on('module.employee.saved', load, $scope);

		function load() {
			ssAsync(function*() {
				vm.loading = true;
				if (org == null) {
					org = yield ssOrganizationSvc.getActiveOrg();
				}
				if (vm.emp == null) {
					vm.emp = yield ssEmployeeApi.get({
						id: $stateParams.employeeId,
						fields: 'hireDate',
						include: 'phoneNumbers,emails,positions,jobTypes,statusHistory,assignments,supervisors'
					}, null, false);
				} else {
					yield vm.emp.$refresh();
				}

				vm.permitted = vm.emp.$permitted.props;

				vm.hireDateTxt = vm.emp._hireDate != null ? ssDateByOrgHlp.utcIsoToRelativeMoment(vm.emp._hireDate, org).format('MM/DD/YYYY') : null;
				vm.emp.currentSupervisor = vm.emp._supervisors.find(x => ssDateByOrgHlp.isCurrent({ obj: x, fromProp: '_from', toProp: '_to', org }));

				vm.currentStatus = vm.emp._statusHistory.find(x => ssDateByOrgHlp.isCurrent({ obj: x, fromProp: '_from', toProp: '_to', org }));
				if (vm.currentStatus != null) {
					vm.currentStatus.name = enumEmployeeStatus.byValue(vm.currentStatus._statusId);
				}

				if (vm.emp.$permitted.props.assignments.view) {
					getAssignment();
				}
				vm.loading = false;
			})();
		}

		function getAssignment() {
			ssAsync(function*() {
				try {
					let positions = yield ssPositionsApi.getForEmployee({
						id: vm.emp.id,
						include: 'assignments'
					});

					positions.sort((a, b) => {
						return new Date(b._startDate) - new Date(a._startDate);
					});

					let assignments = yield positions.map((position) => {
						return getAssignmentWithDetails(position, position._assignments[0].id);
					});

					if (assignments) {
						positions.forEach((p, i) => {
							p.assignment = assignments[i];
							// ** We must remove positions from here as this creates a circular reference during JSON serialization **
							//	this is an artifact of mutators...
							delete p.assignment.position;
							// ** We must remove positions from here as this creates a circular reference during JSON serialization **
						});
					}

					vm.emp.primaryPosition = positions.find(p => p.assignment._isPrimary && ssDateByOrgHlp.isCurrent({
						obj: p.assignment,
						fromProp: '_startDateUtc',
						toProp: '_endDateUtc',
						org
					}));

					if (vm.emp.primaryPosition == null) { return; }

					vm.emp.primaryAssignment = vm.emp.primaryPosition.assignment;

					ssListSvc.getByExternalId('position.types').then(types => {
						vm.emp.primaryAssignment.name = ssListSvc.mapName(vm.emp.primaryPosition._name, types);
					});
				} catch (error) {
					console && console.error && console.error('Error getting assignments', error);
				}
			})();
		}

		function getAssignmentWithDetails(position, assignId) {
			return ssAsync(function*() {

				let assignment = yield ssAssignmentApi.getAssignment({
					id: position.id,
					assignmentId: assignId,
					include: 'details'
				});
				if (!assignment) { return null; }
				assignment.initialDetails = assignment._details.find(d => d.id === assignment._currentDetailsId);

				// TODO REMOVE THIS CODE FOLLOWING.
				// -----------------------------------------------
				//assignment.initialDetails._payRate = null;
				//assignment.initialDetails._payRateFrequency = null;
				// -----------------------------------------------

				if (assignment.initialDetails != null) {
					assignment.currentDetails = [
						'_assignmentId',
						'_fTE',
						'_hoursPerDay',
						'_paidHolidaysPerSchoolYear',
						'_payLane',
						'_payPeriodsPerYear',
						'_payStep',
						'_personalDaysPerSchoolYear',
						'_sickDaysPerSchoolYear',
						'_fromDateUtc',
						'_toDateUtc',
						'_vacationDaysPerSchoolYear',
						'_workDaysPerYear',
						'_payRate',
						'_payRateFrequency'
					].reduce(function(prev, key) {
						prev[key] = assignment.initialDetails[key];
						return prev;
					}, {});
				} else {
					assignment.currentDetails = {};
				}

				assignment.position = position;

				let jobTypeId = assignment.position._jobType.id;
				assignment.jobType = yield ssJobTypeSvc.getJobTypeById(jobTypeId);

				assignment.currentJobTypeDetails = assignment.jobType._currentJobTypeDetail;

				return assignment;
			})();
		}
	}
}

// #-- END Segment (/employee-record/segment-sidebar) --# //
// #----------------------------------------------------# //

angular.module('ec-employee-segments').directive('segmentEmployeeRecordSidebar', () => new Sidebar('sidebar'));
