import { Segment } from 'base-segment';
// #------------------------------------------# //
// #---- Segment (/index/segment-results) ----# //

class Results extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $rootScope, moment, ssAsync, ssDebounce, ssEmployeeApi, enumEmployeeStatus, ssListSvc, sortFilterHlp, ssEmployeeAssignmentHlp, ssDateByOrgHlp, ssToastHlp) {

		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;
		const org = routeVm.org;
		// hookup segment logic
		const logic = $injector.get('segmentIndexResultsLogic'); // jshint ignore:line

		const vm = $scope.vm = {
			employees: [],
			showInfScroll: true,
			loading: false,
			showNameLinks: true
		};
		
		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			/**
			* Generates an href for the directory/report name hyperlink based upon feature flags and the state of the employee data
			* @param {Object} emp - the Employee object representing a row in the directory/report data set
			*/
			getHref(emp) {
				var ret = `/ec-employee/record/${emp.id}`;

				if (routeVm.isGlobalDirectory) {
					// If we don't have an orgPersonId for some reason set the url to an empty string
					ret = emp._orgPersonId ? `/ec-directory/person-detail/${emp._orgPersonId}/personal` : '';
				}
				return ret;
			},

		//	Gets the employee name
			getEmployeeName(e) {
				let ret = e._lastName == null ? '' : e._lastName;
				if (ret.length && e._firstName != null && e._firstName.length) { ret += ', '; }
				ret += e._firstName == null ? '' : e._firstName;
				return ret;
			},
			/**
			* Should we render the employee name as a link on the employee directory / employee data report
			* @param emnployee {object} the employee object representing a row in the grid
			* @remarks - if a DIP org has null orgIds don't show broken links, just show text.
			**/
			showNameLinks(employee) {
				return routeVm.isGlobalDirectory && employee._orgPersonId == null ? false : true;
			}
		};

		const pageSize = 50;
		var firstRun = true;
		var currentPage = 0;
		var currentRequest = null;
		var positionTypes = [];

		ssListSvc.getByExternalId('position.types').then(function(types) {
			positionTypes = types;
		});

		const debounce = ssDebounce(() => {
			filterResults();
		}, 350);

		$scope.enumEmployeeStatus = enumEmployeeStatus; // jshint ignore:line

		$scope.$watch('routeVm.location.length', debounce);
		$scope.$watch('routeVm.jobType.length', debounce);
		$scope.$watch('routeVm.department.length', debounce);
		$scope.$watch('routeVm.status.length', debounce);
		$scope.$watch('routeVm.employmentGroup.length', debounce);
		$scope.$watch('routeVm.fromHireDate', debounce);
		$scope.$watch('routeVm.toHireDate', debounce);
		$scope.$watch('routeVm.effectiveDate', debounce);
		$scope.$watch('routeVm.search', debounce);
		$scope.$watch('routeVm.sortBy', debounce);

		let employeeInclude = 'phoneNumbers,emails,positions,jobtypes,locations,assignments';

		if ($scope.$root.isPermitted.employees.edit) {
			employeeInclude += ',statusHistory';
		}

		$scope.refreshResults = refreshResults;

		function refreshResults() {
			if (!vm.showInfScroll || vm.loading) { return; }
			if (currentRequest != null) {
				currentRequest.$abort();
				currentRequest = null;
			}
			// set to prevent multiple calls from firing at the same time on scroll
			vm.loading = true;

			// Tell listeners (the filter segment) that the page is loading data
			$rootScope.$broadcast('employee-directory-loading', { loading: true });
			currentRequest = ssAsync(function*() {
				try {
					var searchString = '';
					if (typeof routeVm.search === 'string') {
						searchString = routeVm.search.trim();
					}

					currentPage++;

					// Get the hire dates setup correctly for use in the filter.
					//	* ensure that they are either NULL (not passed in filter) or
					//	* Noon Org time in UTC
					const fromHireDate = routeVm.fromHireDate ? ssDateByOrgHlp.startOfDayRelativeTo(new moment(routeVm.fromHireDate), org) : null;
					const toHireDate = routeVm.toHireDate ? ssDateByOrgHlp.endOfDayRelativeTo(new moment(routeVm.toHireDate), org) : null;

					var searchFilter = Object.assign({}, {
						location: sortFilterHlp.filterOrNullForPost(routeVm.location),
						jobtype: sortFilterHlp.filterOrNullForPost(routeVm.jobType),
						department: sortFilterHlp.filterOrNullForPost(routeVm.department),
						status: sortFilterHlp.filterOrNullForPost(routeVm.status),
						employmentgroup: sortFilterHlp.filterOrNullForPost(routeVm.employmentGroup),
						fullname: searchString,
						hiredatestart: fromHireDate,
						hiredateend: toHireDate
					})
					Object.keys(searchFilter).forEach((key) => (searchFilter[key] == null) && delete searchFilter[key]);
					
					var sortFilter = Object.assign({}, {
						[routeVm.sortBy.field]: routeVm.sortBy.dir
					})

					var filter = Object.assign({}, {
							fields: 'firstName,lastName,hireDate',
							include: employeeInclude,
							offset: (currentPage - 1) * pageSize,
							limit: pageSize,
							filter: searchFilter,
							sort: sortFilter
						}
					);

					var asOfTimeUtc = routeVm.effectiveDate ? ssDateByOrgHlp.safeAsOfDateRelativeTo(new moment(routeVm.effectiveDate).startOf('day'), org) : '';

					// if the as of date is the same as today then we don't need the as of date
					if (moment(asOfTimeUtc).format('MM/DD/YYYY') !== ssDateByOrgHlp.safeNowRelativeTo(org).format("MM/DD/YYY")) {
						// add the asOfTimeUtc
						filter = Object.assign(filter, { 'asOfTimeUtc': asOfTimeUtc });
					}

					let newEmps = yield ssEmployeeApi.search({}, filter);
					try {
						routeVm.totalEmployees = parseInt(newEmps.$headers['total-results'], 10);
					} catch (e) { routeVm.totalEmployees = 0; }

					// In order to effective date the directory, we need to mess with the effective date filters below with this guy
					// If we don't have an effective date set make it null, if we do set it to the end of the day
					let filterEffectiveDate = (!routeVm.effectiveDate) ? null : ssDateByOrgHlp.safeAsOfDateRelativeTo(new moment(routeVm.effectiveDate).startOf('day'), org); // nullable

					newEmps.forEach(employee => {
						employee.hireDateTxt = employee._hireDate != null ? ssDateByOrgHlp.utcIsoToRelativeMoment(employee._hireDate, org).format('MM/DD/YYYY') : null;

						if (employee._statusHistory != null && employee._statusHistory.length) {
							employee.currentStatus = employee._statusHistory.find(x => ssDateByOrgHlp.isCurrent({ obj: x, asOf: filterEffectiveDate, fromProp: '_from', toProp: '_to', org }));
							if (employee.currentStatus != null) {
								employee.currentStatus.name = enumEmployeeStatus.byValue(employee.currentStatus._statusId);
							}
						}

						employee.primaryAssignment = ssEmployeeAssignmentHlp.getPrimaryAssignment(employee, { _asOf: filterEffectiveDate }, org);
						if (employee.primaryAssignment != null) {
							let positionId = employee.primaryAssignment._positionId;
							employee.position = employee._positions.find(p => p.id === positionId);
							if (employee.position) {
								employee.position.name = ssListSvc.mapName(employee.position._name, positionTypes);
							}
						}
					});
				//	concact retrieved employees into the existing collection
					vm.employees = vm.employees.concat(newEmps);
				} catch (e) {
					console && console.error && console.error('Error getting employees', e);
					ssToastHlp.error('There was an error retrieving employees', '')
					vm.showInfScroll = false;
				} finally {
					vm.loading = false;
				//	show the 'no employees found' message based on the length of results
					vm.showEmptyMessage = !vm.employees.length;
				//	figure out when we've reached the end of the list
					vm.showInfScroll = vm.employees.length < routeVm.totalEmployees;
					firstRun = false;
					currentRequest = null;
				//	Tell listeners (the filter segment) that the page is done loading data
					$rootScope.$broadcast('employee-directory-loading', { loading: false });
				}
			})();
		}

		function filterResults() {
			reset();
			refreshResults();
		}

		function reset() {
			if (firstRun) { return; }
			currentPage = 0;
			vm.employees = [];
			vm.showInfScroll = true;
			vm.loading = false;
		}
	}
}

// #-- END Segment (/index/segment-results) --# //
// #------------------------------------------# //

angular.module('ec-employee-segments').directive('segmentIndexResults', () => new Results('results'));
