import {
	Segment
} from 'base-segment';
// #-----------------------------------------# //
// #---- Segment (/index/segment-filter) ----# //

class Filter extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $state, sleepHlp, $timeout, $window, ssAsync, enumEmployeeStatus, ssStringHlp, ssValidationHlp,
		ssLocationSvc, ssJobTypeSvc, ssDepartmentSvc, ssEmployeeApi, ssToastHlp,
		ssOptionalFieldGroupHlp, ssModalSvc, moment, sortFilterHlp, ssEnum, ssListSvc,
		ssDateByOrgHlp, ssOrganizationSvc, ssMultiInitiativeFeatureDetectorSvc) {
		var addEmployee;
		var org;

		const ssnDashRegex = /^\d{3}-\d{2}-\d{4}$/;
		const ssnNoDashRegex = /^\d{9}$/;
		const routeVm = $scope.routeVm;
		const emailType = ssEnum.emailType;
		// hookup segment logic
		const logic = $injector.get('segmentIndexFilterLogic'); // jshint ignore:line

		const validator = $scope.validator = {
			ssn: ssValidationHlp.ssn(),
			email: ssValidationHlp.email(),
			required: ssValidationHlp.required(),
			externalId: ssValidationHlp.string({
				maxLength: 100
			})
		};

		const vm = $scope.vm = {
			canCreateEmployee: ssMultiInitiativeFeatureDetectorSvc.canCreateEmployee(),
			showEmploymentGroups: ssMultiInitiativeFeatureDetectorSvc.showEmploymentGroups(),
			totalEmployeesAriaLabel: '',
			disableEmploymentGroup: false,
			disableJobType: false,
			loading: true,
			formOpened: true,
			errors: '',
			status: enumEmployeeStatus.getEnums().map(o => {
				return {
					_name: ssStringHlp.toProperCase(o.name.toLowerCase()),
					id: o.value
				};
			}),
			saving: false,
			search: '',
			showClear: false,
			disableSearch: true, // disable search until the first time results loads; enable after
			emp: {},
			sortBy: 0,
			sortByOptions: [{
				name: 'Last Name A-Z',
				ariaLabel: 'Sort directory by Last name ascending',
				value: {
					field: 'lastname',
					dir: 'asc'
				}
			}, {
				name: 'Last Name Z-A',
				ariaLabel: 'Sort directory by Last name descending',
				value: {
					field: 'lastname',
					dir: 'desc'
				}
			}, {
				name: 'Hire Date',
				ariaLabel: 'Sort directory by hire date',
				value: {
					field: 'hiredate',
					dir: 'desc'
				}
			}],

			showStatusFilter: $scope.$root.isPermitted.employees.edit && $scope.$root.isPermitted.employees.view
		};

		if (!$scope.$root.isPermitted.employees.edit) {
			// remove hire date
			vm.sortByOptions.splice(2, 1);
		}

		// default to active employees
		if (routeVm.status == null || routeVm.status.length === 0) {
			routeVm.status = [vm.status.find(s => s.id === enumEmployeeStatus.active)];
		}
		routeVm.status.forEach(option => {
			option.checked = true;
		});

		// default to name
		routeVm.sortBy = vm.sortByOptions[0].value;

		// Prepare the data for the dropdowns
		ssAsync(function* () {
			try {
				vm.location = yield ssLocationSvc.getAll();
				vm.jobType = yield ssJobTypeSvc.getAll();
				vm.department = yield ssDepartmentSvc.getAll();
				ssListSvc.getByExternalId('jobtype.employmentgroups').then(data => {
					vm.employmentGroups = data.map((v) => {
						return {
							'_name': v.name,
							'id': v.value
						}
					});
				});
				org = yield ssOrganizationSvc.getActiveOrg();
			} catch (e) {
				console.error('ERROR', e);
			}
		})();

		const go = $scope.go = { // jshint ignore:line
			export () {
				let content = 'Generate and download report for {0} employee{1}?';
				content = content.replace('{0}', routeVm.totalEmployees)
					.replace('{1}', routeVm.totalEmployees === 1 ? '' : 's');
				var modalConfig = {
					title: '',
					content: content,
					primaryText: 'Download',
					secondaryText: 'Cancel',
					size: 'medium'
				};

				var searchString = '';
				if (typeof routeVm.search === 'string') {
					searchString = routeVm.search.trim();
				}

				// Get the hire dates setup correctly for use in the filter.
				//	* ensure that they are either NULL (not passed in filter) or
				//	* Noon Org time in UTC
				const fromHireDate = routeVm.fromHireDate ? ssDateByOrgHlp.startOfDayRelativeTo(moment(routeVm.fromHireDate), org) : null;
				const toHireDate = routeVm.toHireDate ? ssDateByOrgHlp.endOfDayRelativeTo(moment(routeVm.toHireDate), org) : null;

				var filter = Object.assign({}, {
						include: 'StatusHistory,EmploymentHistory,Addresses,PhoneNumbers,Emails,EmergencyContacts,Credentials,positions,assignments,locations,jobtypes,payschedules,supervisors',
						limit: 999999999
					},
					sortFilterHlp.sortOrNull(routeVm.sortBy),
					sortFilterHlp.filterOrNull(routeVm.location, 'location'),
					sortFilterHlp.filterOrNull(routeVm.jobType, 'jobType'),
					sortFilterHlp.filterOrNull(routeVm.department, 'department'),
					sortFilterHlp.filterOrNull(routeVm.status, 'status'),
					sortFilterHlp.filterOrNull(routeVm.employmentGroup, 'employmentGroup'),
					sortFilterHlp.searchFilter(searchString, 'firstname'),
					sortFilterHlp.searchFilter(searchString, 'lastname'),
					sortFilterHlp.filterDateOrNull(fromHireDate, 'hiredatestart'),
					sortFilterHlp.filterDateOrNull(toHireDate, 'hiredateend')
				);

				var asOfTimeUtc = routeVm.effectiveDate ? ssDateByOrgHlp.safeAsOfDateRelativeTo(moment(routeVm.effectiveDate).startOf('day'), org) : '';

				// if the as of date is the same as today then we don't need the as of date
				if (moment(asOfTimeUtc).format('MM/DD/YYYY') !== ssDateByOrgHlp.safeNowRelativeTo(org).format("MM/DD/YYY")) {
					// add the asOfTimeUtc
					filter = Object.assign(filter, {
						'asOfTimeUtc': asOfTimeUtc
					});
				}

				ssModalSvc.showStandard(modalConfig).then(() => {
						$scope.$emit('please-wait', filter);
					},
					(err) => {
						console.error(err);
					});
			},
			validateOptional(field) {
				var ret = true;
				if (field == null) {
					return false;
				}

				return ret;
			},
			validateOr() {
				return ssOptionalFieldGroupHlp.validate([vm.emp._sSN, vm.emp._externalId], 'Please enter a SSN or an External ID.');
			},
			clear() {
				routeVm.location = [];
				routeVm.jobType = [];
				routeVm.department = [];
				routeVm.employmentGroup = [];
				routeVm.fromHireDate = '';
				routeVm.toHireDate = '';
				routeVm.effectiveDate = ''
				routeVm.search = '';

				vm.location.forEach(uncheck);
				vm.jobType.forEach(uncheck);
				vm.department.forEach(uncheck);
				vm.status.forEach(uncheck);

				// on clear, if we are showing the status filter, then do a real clear on status
				if (vm.showStatusFilter) {
					routeVm.status = [];
				} else {
					// otherwise default to active search
					routeVm.status = [vm.status.find(s => s.id === enumEmployeeStatus.active)];
					routeVm.status.forEach(option => {
						option.checked = true;
					});
				}

				// default to name
				routeVm.sortBy = vm.sortByOptions[0].value;
			},

			asyncCheck(val) {
				var err = [];

				if (val.length > 10) {
					return true;
				}
				if (val.length < 10) {
					err.push('needs to be at least 10');
				}
				if (val.length < 5) {
					err.push('keep going');
				}
				return err;
			},
			close() {
				vm.formOpened = false;
				vm.errors = '';
				vm.emp = {};
				$scope.$emit('ss-menu-close');
			},
			validateExternalId() {
				if (vm.emp._externalId == null) {
					return true;
				}

				return validator.externalId(vm.emp._externalId);
			},
			add() {
				if (addEmployee == null) {

					if ((vm.emp._sSN != null && vm.emp._sSN === '') && (vm.emp._externalId != null && vm.emp._externalId === '')) {
						if (vm.emp._sSN != null && !ssnDashRegex.test(vm.emp._sSN) && !ssnNoDashRegex.test(vm.emp._sSN)) {
							vm.errors = 'SSN is not formatted correctly. ###-##-####';
							return;
						}
					}

					// final check for valid form
					if (!go.validateOr(vm.emp._sSN, vm.emp._externalId)) {
						vm.errors = 'Please enter a SSN or an External ID.';
						return;
					}

					vm.saving = true;
					if (ssnNoDashRegex.test(vm.emp._sSN)) {
						// add dashes
						vm.emp._sSN = vm.emp._sSN.substring(0, 3) + '-' + vm.emp._sSN.substring(3, 5) + '-' + vm.emp._sSN.substring(5);
					}
					addEmployee = ssAsync(function* () {
						try {
							let employee = yield ssEmployeeApi.add({
								include: 'emails'
							}, vm.emp);
							if (vm.emp._workEmail) {
								employee._emails.push({
									_emailAddress: vm.emp._workEmail,
									_emailType: emailType.work,
									_isPrimary: false
								});
							}
							if (vm.emp._personalEmail) {
								employee._emails.push({
									_emailAddress: vm.emp._personalEmail,
									_emailType: emailType.personal,
									_isPrimary: false
								});
							}
							yield employee._emails.$save();
							yield sleepHlp.sleep();
							if (employee.id != null) {
								$state.go('base.employeeRecord.personal', {
									employeeId: employee.id,
									action: '-edit'
								});
							} else {
								addEmployee = null;
								vm.saving = false;
								ssToastHlp.error('There was an error adding the employee. Please try again later.');
							}
						} catch (err) {
							addEmployee = null;
							vm.saving = false;
						}
					})();
				}
			}
		};

		$scope.$watch('routeVm.totalEmployees', (nv, ov) => {
			if (!nv) {
				nv = 0;
			}
			vm.totalEmployeesAriaLabel = nv + ' Employees found'
			if (nv === 1) {
				vm.totalEmployeesAriaLabel = nv + ' Employee found'
			}
		});

		$scope.$on('please-wait', (event, val) => {
			var tempUrl;

			var modalConfig = {
				title: 'Please wait...',
				content: '',
				size: 'small'
			};

			$timeout(() => {
				ssModalSvc.showWait(modalConfig);

				ssAsync(function* () {
					tempUrl = yield ssEmployeeApi.exportEmployee(val);
				})().then(() => {
					try {
						$window.open(tempUrl._string, '_blank', '');
					} catch(e) {}
					ssModalSvc.clearDialog();
				});
			}, 100);
		});

		$scope.$watch('vm.emp._sSN', (nv, ov) => {
			if (nv == null) {
				return;
			}
			var regex = /^(\d{3})\s?\-?\s?(\d{2})\s?\-?\s?(\d{4})$/;
			var formatter = '$1-$2-$3';
			if (nv.length > 11) {
				nv = nv.substring(0, 11);
			}
			vm.emp._sSN = nv.replace(regex, formatter);

		});

		$scope.$on('ss-menu-closed', (event, elem, ddElem) => {
			if (!vm.formOpened) {
				return;
			}
			if (ddElem.hasClass('create-form')) {
				vm.formOpened = false;
				$scope.$root.safeDigest($scope);
			}
		});

		$scope.$on('employee-directory-loading', (event, val) => {
			vm.loading = val.loading;
		});

		$scope.$watchGroup(['routeVm.location.length', 'routeVm.jobType.length', 'routeVm.department.length',
			'routeVm.status.length', 'routeVm.search', 'routeVm.sortBy', 'routeVm.employmentGroup.length', 'routeVm.fromHireDate',
			'routeVm.toHireDate', 'routeVm.effectiveDate'
		], (nv, ov) => {
			vm.showClear = routeVm.location.length > 0 ||
				routeVm.jobType.length > 0 ||
				routeVm.department.length > 0 ||
				(routeVm.search != null && routeVm.search.length > 0) ||
				routeVm.sortBy !== vm.sortByOptions[0].value ||
				routeVm.fromHireDate ||
				routeVm.toHireDate ||
				routeVm.employmentGroup.length > 0 ||
				routeVm.effectiveDate;

			// We can only search by job type or employment group.  not both.  Deselect the other ones
			if (routeVm.jobType.length > 0) {
				vm.disableEmploymentGroup = true;
				vm.disableJobType = false;
				if (routeVm.employmentGroup.length > 0) {
					routeVm.employmentGroup = [];
				}
			}
			if (routeVm.jobType.length === 0) {
				vm.disableEmploymentGroup = false;
			}
			if (routeVm.employmentGroup.length > 0) {
				vm.disableEmploymentGroup = false;
				vm.disableJobType = true;
				if (routeVm.jobType.length > 0) {
					routeVm.jobType = [];
				}
			}
			if (routeVm.employmentGroup.length === 0) {
				vm.disableJobType = false;
			}

			if (vm.showClear) {
				return;
			}

			if (vm.showStatusFilter) {
				vm.showClear = routeVm.status.length > 0;
			} else {
				// show clear if not default status
				vm.showClear = !(routeVm.status.length === 1 && routeVm.status[0].id === enumEmployeeStatus.active);
			}
		});

		// broadcast from segment-results
		$scope.$on('employee-directory-loading', (event, args) => {
			if (args != null && args.loading === false) {
				vm.disableSearch = false;
			}
		});

		function uncheck(item) {
			item.checked = false;
		}
	}
}


// #-- END Segment (/index/segment-filter) --# //
// #-----------------------------------------# //

angular.module('ec-employee-segments').directive('segmentIndexFilter', () => new Filter('filter'));
