/*@ngInject*/
function empReportsApi($http, ssApiSvc, buildConfig) {
	// #--------------------------------------# //
	// #---- API Endpoint (empReportsApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.employees;
	const credentialsUrl = `${baseUrl}/employeeCredentials`;
	const assignmentsUrl = `${baseUrl}/export/assignments`;
	const demographicsUrl = `${baseUrl}/export/employeedemographics`;

	// possible relationships
	const rel = [];

	// generate endpoints
	const getCredentials = ssApiSvc.endpoint({ url: credentialsUrl, method: 'GET' }, 'credential', rel);
	const getCredentialsPost = ssApiSvc.endpoint({ url: credentialsUrl, method: 'POST'}, 'credential', rel);
	const getAssignments = ssApiSvc.endpoint({ url: assignmentsUrl, method: 'GET' }, 'assignments', rel);
	const getAssignmentsPost = ssApiSvc.endpoint({ url: assignmentsUrl, method: 'POST' }, 'assignments', rel);
	const getDemographics = ssApiSvc.endpoint({ url: demographicsUrl, method: 'GET' }, 'demographics', rel);
	const getDemographicsPost = ssApiSvc.endpoint({ url: demographicsUrl, method: 'POST' }, 'demographics', rel);

	/**
	 * Makes a call to the Employee Credentials Report endpoint, and returns the data as a CSV
	 * @param {*} id Ignored
	 * @param {string} filter The filter (pre-encoded) that will be applied to the report endpoint to get a result set as CSV
	 */
	const exportCredentialsCsv = (id, filter) => {
		let url = `${credentialsUrl}/csv`;
		return exportCsv(url, filter);
	};

	/**
	 * Makes a call to the Employee Export endpoint, and returns the data as a CSV
	 * @param {*} id
	 * @param {string} filter The filter (pre-encoded) that will be applied to the report endpoint to get a result set as CSV
	 */
	const exportAssignmentsCsv = (id, filter) => {
		let url = `${assignmentsUrl}/csv${id == null ? '' : '/' + id}`;
		return exportCsv(url, filter);
	};

	/**
	 * Makes a call to the Employee Export endpoint, and returns the data as a CSV
	 * @param {*} id
	 * @param {string} filter The filter (pre-encoded) that will be applied to the report endpoint to get a result set as CSV
	 */
	const exportDemographicsCsv = (id, filter) => {
		let url = `${demographicsUrl}/csv${id == null ? '' : '/' + id}`;
		return exportCsv(url, filter);
	};

	/**
	 * Makes a call to an endpoint, and returns the data as a CSV
	 * @param {*} url
	 * @param {string} filter The filter (pre-encoded) that will be applied to the report endpoint to get a result set as CSV
	 */
	const exportCsv = (url, filter) => {
		let urlConfig = {
			method: 'GET',
			url: `${url}?${filter}`
		};
		return $http(urlConfig);
	};

	// API endpoint methods
	return {
		getCredentials,
		getCredentialsPost,
		getAssignments,
		getAssignmentsPost,
		getDemographics,
		getDemographicsPost,
		exportCredentialsCsv,
		exportAssignmentsCsv,
		exportDemographicsCsv
	};

	// #-- END API Endpoint (empReportsApi) --# //
	// #--------------------------------------# //
}

angular.module('ec-employee-apis').factory('empReportsApi', empReportsApi);
