/*@ngInject*/
function logic(moment, ssDateByOrgHlp, ssListSvc) {

	let positionTypes = [];
	ssListSvc.getByExternalId('position.types').then(types => {
		positionTypes = types;
	});

	// logic api
	return {
		processCredential
	};

	/**
	 *
	 * @param {Object} cred
	 */
	function processCredential(cred, org) {
		cred.issuedTxt = cred._stateStartedDay != null ? ssDateByOrgHlp.startOfDayRelativeTo(moment(cred._stateStartedDay), org) : null;
		cred.expirationTxt = cred._expirationDay != null ? ssDateByOrgHlp.startOfDayRelativeTo(moment(cred._expirationDay), org) : null;
		cred.subjects = cred._subjects != null ? cred._subjects.map(sub => sub.name).join(', ') : '';

		cred._employee.positionNameTxt = '';

		if (cred._employee.primaryJobTypeName != null) {
			cred._employee.positionNameTxt += cred._employee.primaryJobTypeName;
		}

		if (cred._employee.primaryPositionName != null) {
			if (cred._employee.positionNameTxt.length) { cred._employee.positionNameTxt += ', '; }
			cred._employee.positionNameTxt += ssListSvc.mapName(cred._employee.primaryPositionName, positionTypes);
		}

		if (cred._employee.stateEducatorId != null && cred._employee.stateEducatorId.length) {
			if (cred._employee.positionNameTxt.length) { cred._employee.positionNameTxt += ' | '; }
			cred._employee.positionNameTxt += cred._employee.stateEducatorId;
		}
	}
}
angular.module('ec-employee-segment-logic').factory('segmentReportsCredentialsContentLogic', logic);
