import {Segment} from 'base-segment';
// #---------------------------------------------------# //
// #---- Segment (/employee-record/segment-header) ----# //

class Header extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $state, $location, ssAsync, ssEventSvc, ssModalSvc, ssToastHlp, ssObjectHlp, ssMultiInitiativeFeatureDetectorSvc) {

		// view model that is shared by all segments on this route, used for segment coordination
		const routeVm = $scope.routeVm;

		const permissions = $scope.permissions = routeVm.emp.$permitted;
		const viewingOwnProfile = routeVm.loggedInUserKey === routeVm.emp.id;

		// hookup segment logic
		const logic = $injector.get('segmentEmployeeRecordHeaderLogic');

		// view model for tracking state
		const vm = $scope.vm = {
			fullName: logic.getName(routeVm.emp),
			canManageEmployee: ssObjectHlp.glean($scope, _ => _.$root.isPermitted.users.view, false),
			canDeleteEmployee: permissions.actions.delete && !viewingOwnProfile && ssMultiInitiativeFeatureDetectorSvc.canDeleteEmployee()
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			manageAccess() {
				$location.path(`/ec-useraccess/user/employee/${routeVm.emp.id}`);
			},
			delete() {
				let confirmation = {
					title: 'Delete this Employee',
					content: 'You are deleting this employee from Frontline Central. Once they are deleted, all information associated with this employee will be removed from Frontline Central and cannot be retrieved.',
					primaryText: 'Delete',
					secondaryText: 'Cancel'
				};
				ssModalSvc.showDestructive(confirmation).then(() => {
					routeVm.refreshing = true;
					ssAsync(function* () {
						try {
							yield routeVm.emp.$remove();
							ssToastHlp.success('Employee has been deleted successfully.');
							$state.go('base.index');
						} catch (e) {
							ssToastHlp.error('There was an error deleting the employee. Please try again later.');
						} finally {
							routeVm.refreshing = false;
						}
					})();
				});
			}
		};

		// the employee is refreshed, update the name we show
		ssEventSvc.on('ss-json-api.postSave.employees', emp => {
			vm.fullName = logic.getName(emp);
		}, $scope);
	}
}

// #-- END Segment (/employee-record/segment-header) --# //
// #---------------------------------------------------# //

angular.module('ec-employee-segments').directive('segmentEmployeeRecordHeader', () => new Header('header'));
