import { Component } from 'base-component';
// #------------------------------------# //
// #---- Component (cmp-phone-item) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	label: '@',
	phone: '=',
	editMode: '=?',
	onRemove: '&',
	onCheck: '&',
	editable: '=?',
};

class PhoneItem extends Component {
	/*@ngInject*/
	controller($scope, ssModalSvc, ssEnum, ssValidationHlp) {
		// view model for tracking state
		const vm = $scope.vm = {
			options: {
				phoneTypes: ssEnum.phoneTypes.getEnums(),
				usageType: ssEnum.usageType.getEnums(),
			},
		};

		$scope.validator = {
			required: ssValidationHlp.required(),
			phoneNumber(val, field) {
				if (val == null || !val.length) { return true; }
				let checkPhone = ssValidationHlp.phone();
				return checkPhone(val, field);
			},
		};

		const phone = $scope.phone;

		$scope.$watch('phone._isPrimary', (nv, ov) => {
			if (nv === ov) { return; }
			if (nv === true) { $scope.onCheck(); }
		});

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			cancel() {
				if (typeof phone.$revert === 'function') {
					phone.$revert();
				}
				if (phone.id == null) {
					$scope.onRemove();
				}
				phone.editable = false;
			},
			edit() { phone.editable = true; },
			remove() {
				let confirmation = {
					title: 'Are you sure?',
					content: 'Are you sure you want to delete this phone number?',
					primaryText: 'Delete Phone Number',
					secondaryText: 'Cancel',
					alwaysOnTop: true
				};
				ssModalSvc.showDestructive(confirmation).then(() => {
					$scope.onRemove();
				});
			},
		};
	}
}

// #-- END Component (cmp-phone-item) --# //
// #------------------------------------# //

angular.module('ec-employee-components').directive('cmpPhoneItem', () => new PhoneItem(props, 'phone-item'));
