/*@ngInject*/
function logic() {
	// logic api
	return {
		/**
		 * Filter out all of the distinct Employment Groups from the list of job types
		 *
		 * @param {array} jobTypes - The array of job type objects to distill distinct employment groups from
		 * @return {array} unique employment groups for the given job types
		 */
		filterBarganingUnits: (jobTypes) => {
			let result = [];

			jobTypes.forEach(element => {
				if (element.employmentGroup && element.employmentGroup.id.length) {

					if (!result.find((item) => {
						return item._name === element.employmentGroup.name
					})) {
						result.push({
							_name: element.employmentGroup.name,
							id: element.employmentGroup.id
						});
					}
				}
			});

			return result
		}
	};
}
angular.module('ec-employee-segment-logic').factory('segmentIndexFilterLogic', logic);
