import { Segment } from 'base-segment';
// #---------------------------------------------------------------# //
// #---- Segment (/reports/demographic/report/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($rootScope, $injector, $scope, $location, $window, ssEmployeeApi, ssDebounce, ssAsync, ssEventSvc, ssMultiInitiativeFeatureDetectorSvc, ssMutatorDataHlp, empReportsApi, reportHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentReportsDemographicReportContentLogic'); // jshint ignore:line

		const pageSize = 50;

		let currentRequest = null;

		// view model for tracking state
		const vm = $scope.vm = {
			loading: true,
			employees: [],

			// changes to this object will result in data change
			filters: {},

			pages: 0,

			isGlobalDirectory: ssMultiInitiativeFeatureDetectorSvc.isDataIntegrationsEnabled(),

			filterConfig: {
				countLabel: 'Employee',
				searchLabel: 'Employee',
				useFilter: ['effectiveDate', 'empStatus', 'empStatusReasons', 'lastUpdated'],
				sortOptions: [{
					name: 'Last Name A-Z',
					ariaLabel: 'Sort report by last name ascending',
					value: {
						field: reportHlp.sortKeys.LAST_NAME,
						dir: 'asc'
					},
				}, {
					name: 'Last Name Z-A',
					ariaLabel: 'Sort report by last name descending',
					value: {
						field: reportHlp.sortKeys.LAST_NAME,
						dir: 'desc'
					},
				}, {
					name: 'Last Updated Asc',
					ariaLabel: 'Sort report by last updated date ascending',
					value: {
						field: reportHlp.sortKeys.EMP_LAST_UPDATED,
						dir: 'asc'
					}
				}, {
					name: 'Last Updated Desc',
					ariaLabel: 'Sort report by last updated date descending',
					value: {
						field: reportHlp.sortKeys.EMP_LAST_UPDATED,
						dir: 'desc'
					}
				}, {
					name: 'Hire Date Asc',
					ariaLabel: 'Sort report by hire date ascending',
					value: {
						field: reportHlp.sortKeys.HIRE_DATE,
						dir: 'asc'
					}
				}, {
					name: 'Hire Date Desc',
					ariaLabel: 'Sort report by hire date descending',
					value: {
						field: reportHlp.sortKeys.HIRE_DATE,
						dir: 'desc'
					}
				}],
				defaultSortIndex: 0,

				exportFileName: 'employeeDemographicExport.csv',
				export: empReportsApi.exportDemographicsCsv,
			},
		};

		const debounce = ssDebounce((nv, ov) => {
			if (nv === ov) { return; }
			go.filterResults({ currentPage: 1 });
		}, 350);

		$scope.$watch('vm.filters', debounce, true);

		ssEventSvc.on('location-change-start', (event, toUrl) => {
			if (toUrl.indexOf('today') > -1) {
				event.preventDefault();
				$window.location = toUrl;
			}
		}, $rootScope);

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			filterResults,

			/**
			 * For DIP enabled orgs get the employee org person id and then go to the global directory
			 * @param {String} empId
			 */
			toProfile(empId) {
				if (!vm.isGlobalDirectory) { return; }
				ssEmployeeApi.get({ id: empId, fields: 'orgPersonId' }, null, false).then(emp => $location.url(`/ec-directory/person-detail/${emp._orgPersonId}/personal`));
			},

			formatPhone(phone) {
				return ssMutatorDataHlp.formatPhone(phone);
			},

			formatDate(date) {
				let val = new Date(date + 'Z');
				return val.toLocaleString();
			},

			getDateString: reportHlp.getDateString
		};

		function filterResults(event) {
			vm.currentPage = event.currentPage;
			if (currentRequest != null) {
				currentRequest.$abort();
				currentRequest = null;
			}
			currentRequest = ssAsync(function*() {
				vm.loading = true;

				try {
					let querystring = Object.assign({}, {
						organizationId: routeVm.org.id
					});

					let body = Object.assign({}, {
						offset: (event.currentPage - 1) * pageSize,
						limit: pageSize,
						filter:{
							asofutctime: vm.filters["filter[asOfTimeUtc]"],
							lastupdatedstart: vm.filters["filter[lastUpdatedStart]"],
							lastupdatedend: vm.filters["filter[lastUpdatedEnd]"],
							statusreason: vm.filters["filter[statusReason]"],
							status: vm.filters["filter[status]"],
							fullname: vm.filters["filter[fullname]"]
						},
						sort: {
							lastname: vm.filters["sort[lastname]"],
							hiredate: vm.filters["sort[hireDate]"],
							lastupdatedutc: vm.filters["sort[lastupdatedutc]"]
						}
					});

					if (!completeLastUpdatedRange()) {
						return;
					}

					// sets the filters in the querystring to make the page linkable
					reportHlp.setUrl($location, querystring);

					vm.employees = yield empReportsApi.getDemographicsPost(querystring, body);

					vm.employees.forEach(e => e.securityGroup = e._securityGroup == null ? '' : e._securityGroup.split(',').join(', '));

					if (vm.employees != null) {
						// set up pagination fields
						vm.filterConfig.totalResults = parseInt(vm.employees.$headers['total-results'], 10);
						vm.filterConfig.totalResults = isNaN(vm.filterConfig.totalResults) ? 0 : vm.filterConfig.totalResults;
						vm.pages = Math.ceil(vm.filterConfig.totalResults / pageSize);
					} else {
						vm.filterConfig.totalResults = 0;
						vm.pages = 0;
					}
				} catch (error) {
					console && console.error && console.error('Error getting demographic', error);
				} finally {
					vm.loading = false;
				}
			})();
		}

		function completeLastUpdatedRange() {
			if (vm.filters['filter[lastUpdatedStart]'] && vm.filters['filter[lastUpdatedEnd]'] == null) {
				return false;
			}

			if (vm.filters['filter[lastUpdatedEnd]'] && vm.filters['filter[lastUpdatedStart]'] == null) {
				return false;
			}

			return true;
		}
	}
}

// #-- END Segment (/reports/demographic/report/segment-content) --# //
// #---------------------------------------------------------------# //

angular.module('ec-employee-segments').directive('segmentReportsDemographicReportContent', () => new Content('content'));
