function credentialFactoryHlp() {
	function Credential() {
		this._code = 100;
		this._endorsements = [];
		this._externalId = null;
		this._from = new Date();
		this._credentialId = '';
		this._to = null;
		this.expired = false;
		this.id = null;
		this.$isDirty = function() {
			return !(this._code === 100 &&
				this._endorsements.length === 0 &&
				this._externalId == null &&
				this._credentialId.length === 0 &&
				this._to == null);
		};
	}

	var factory = {};

	factory.create = function() {
		return angular.extend({}, new Credential());
	};

	factory.createDummy = function() {
		var c = factory.create();
		c.isNew = true;
		c.name = null;
		c.state = null;
		c.filteredQualifications = [];
		return c;
	};

	return factory;
}
angular.module('ec-employee-helpers').factory('credentialFactoryHlp', credentialFactoryHlp);
