import { State } from 'base-state';
// #----------------------------------# //
// #---- State (/employee-record) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {
	emp: null,
	refreshing: false
};

const stateName = 'employeeRecord';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/record/{employeeId:[0-9A-Za-z-]+}',
	templateUrl: 'routes/employee-record/employee-record.html',
	resolve: {
		emp: /*@ngInject*/ function($stateParams, $location, ssAsync, ssEmployeeApi, ssActiveUserSvc, ssEventSvc, ssOrganizationSvc, ssMultiInitiativeFeatureDetectorSvc) {
			return (ssAsync(function*() {
				try {
					routeVm.emp = yield ssEmployeeApi.get({
						id: $stateParams.employeeId,
						fields: 'orgPersonId,firstName,lastName'
					}, null, false);

					if (ssMultiInitiativeFeatureDetectorSvc.isDataIntegrationsEnabled()) {
						let path = $location.path();
						const url = `/ec-directory/person-detail/${routeVm.emp._orgPersonId}`;

						if (path.endsWith('personal')) {
							$location.url(`${url}/personal`);
						} else if (path.endsWith('employment')) {
							$location.url(`${url}/employment`);
						} else if (path.endsWith('credentials')) {
							$location.url(`${url}/credentials`);
						} else if (path.endsWith('medical')) {
							$location.url(`${url}/documents-medical`);
						} else if (path.endsWith('personnel')) {
							$location.url(`${url}/documents-personal`); // yes, personal and not personnel here
						}
					}

					routeVm.loggedInUserKey = yield ssActiveUserSvc.getIdentityByType('employee');
					routeVm.org = yield ssOrganizationSvc.getActiveOrg();
				} catch (e) {
					ssEventSvc.emit('error-404');
					console && console.error && console.error('Error retrieving employee', e);
				}
			})());
		},
		isHRMSEnabled: /*@ngInject*/ function(ssMultiInitiativeFeatureDetectorSvc) {
			routeVm.isHRMSEnabled = ssMultiInitiativeFeatureDetectorSvc.isHRMSEnabled();
		},
		$title: /*@ngInject*/ (emp) => {
			if (emp == null) {
				return 'Frontline Central - Employee Directory';
			}
			return `Frontline Central - ${emp._firstName} ${emp._lastName}`;
		}
	},
	$permissions: (permissionSvc) => permissionSvc.isPermittedAction('employees', 'view'),
	redirectToState: 'base.employeeRecord.personal'
};

// #-- END State (/employee-record) --# //
// #----------------------------------# //

angular.module('ec-employee').config( /*@ngInject*/ ($stateProvider, $urlRouterProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
