export class Segment {
	constructor(segmentName) {
		this.restrict = 'E';
		this.scope = true;
		this.replace = true;
		this.templateUrl = (elem, attrs) => {
			return `routes${attrs.folder}/segment-${segmentName}/${segmentName}.html`;
		};
	}
}

function properToDashCase(txt) {
	return txt
		.replace(/([A-Z])/g, '-$1')
		.substr(1)
		.toLowerCase();
}
