/*@ngInject*/
function logic(ssMultiInitiativeFeatureDetectorSvc, ssFlagSvc) {

	// logic api
	return { canViewEmploymentTab, canViewEmergencyTab, canViewCredentialTab, canViewMedicalFiles, canViewPersonnelTab };

	function canViewMedicalFiles(permissions) {
		if (permissions.filesMedicalBenefits.view) { return true; }
		if (permissions.filesMedicalGeneralHealth.view) { return true; }
		if (permissions.filesMedicalLeave.view) { return true; }
		if (permissions.filesMedicalOther.view) { return true; }
		if (permissions.filesMedicalWorkersComp.view) { return true; }
	}

	function canViewPersonnelTab(permissions) {
		if (permissions.filesPersonnelContracts.view) { return true; }
		if (permissions.filesPersonnelDiscipline.view) { return true; }
		if (permissions.filesPersonnelEmploymentAuth.view) { return true; }
		if (permissions.filesPersonnelEmploymentStatus.view) { return true; }
		if (permissions.filesPersonnelOther.view) { return true; }
		if (permissions.filesPersonnelPayroll.view) { return true; }
		if (permissions.filesPersonnelPolicy.view) { return true; }
		if (permissions.filesPersonnelPreEmployment.view) { return true; }
		if (permissions.filesPersonnelTranscripts.view) { return true; }
		return false;
	}

	function canViewEmploymentTab(permissions) {
		if (permissions.hireDate.view) { return true; }
		if (permissions.seniorityDate.view) { return true; }
		if (permissions.tenureDate.view) { return true; }
		if (permissions.retirementSystem.view) { return true; }
		if (permissions.statusHistory.view) { return true; }
		if (permissions.assignments.view) { return true; }
		if (permissions.supervisor.view) { return true; }
		return false;
	}

	function canViewCredentialTab(permissions) {
		return permissions.employeeCredentials.view && !ssFlagSvc.checkState('hrms-with-erp');
	}

	function canViewEmergencyTab(permissions) {
		return permissions.emergencyContacts.view && ssMultiInitiativeFeatureDetectorSvc.isEmergencyContactsTabEnabled();
	}
}
angular.module('ec-employee-segment-logic').factory('segmentEmployeeRecordContentLogic', logic);
