import {Segment} from 'base-segment';
import { log } from 'util';
// #------------------------------------------------------------------------# //
// #---- Segment (/employee-record/emergency/segment-emergency-content) ----# //

class EmergencyContent extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $stateParams, ssEventSvc) {
		// view model that is shared by all segments on this route, used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentEmployeeRecordEmergencyEmergencyContentLogic');

		$scope.validator = logic.validator;

		// view model for tracking state
		const vm = $scope.vm = {
			employeeId: $stateParams.employeeId,
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			onAfterSave() {
				// emit here for sidebar changes to get picked up
				ssEventSvc.emit('module.employee.saved');
			}
		};
	}

}


// #-- END Segment (/employee-record/emergency/segment-emergency-content) --# //
// #------------------------------------------------------------------------# //

angular.module('ec-employee-segments').directive('segmentEmployeeRecordEmergencyEmergencyContent', () => new EmergencyContent(
	'emergency-content'));
